.media-libray-wrapper {
    .item-wrapper {
        height: calc(100vh - 274px);
        overflow-y: auto;
        overflow-x: hidden;

        .ui-dialog & {
            height: calc( 100vh - 50px - 245px ); // height popin - modal head/footer
        }
    }
    .item-picture {
        position: relative;
        height: 140px;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 1px solid #c5c6c5;
        background: {
            size: contain;
            repeat: no-repeat;
            position: center;
        }

        @include media-breakpoint-down(md) {
            height: 80px;
        }

        img {
            opacity: 0;
        }

        .btn-plugin {
            height: $btn-plugin-w;
            width: $btn-plugin-w;
            border: 1px solid transparent;
            box-shadow: 0px 0 4px 1px rgba(50, 50, 50, 0.3);
            border-radius: 100%;
            background-color: #fff;
            position: relative;
            padding: 7px;
            outline-width: 0;
            line-height: 1;
            @include transition(all $ui-transition);

            i {
                margin: 0;
            }
        }

        .btn-action-wrapper {
            position: absolute;
            left: 0;
            top: 0;
            display: none;

            &:hover {
                & > .more-options {
                    height: 8.5em;
                    width: 5em;
                    opacity: 1;

                    & > .btn-plugin {
                        $btn-plugin-top: 50px;
                        $btn-plugin-interval: 40px;
                        &:nth-child(1) {
                            top: $btn-plugin-top;
                        }
                        &:nth-child(2) {
                            top: $btn-plugin-top + $btn-plugin-interval;
                        }
                    }
                }

                & > .btn-more {
                    color: #fff;
                    background-color: $cloud-brand;
                    border-color: $cloud-brand;
                    .icon {
                        color: #fff !important;
                    }
                }

            }
        }

        &:hover .btn-action-wrapper {
            display: block;
        }

        .more-options {
            position: absolute;
            top: -10px;
            left: -22px;
            opacity: 0;
            @include transition(all 250ms ease);

            .btn-plugin {
                margin-bottom: 5px;
                position: absolute;
                top: 10px;
                left: 22px;
                @include transition(all 250ms ease);
            }
        }
    }
}
