// Panel

.cloud-panel {
  display: inline-block;
  flex-direction: column;
  @include box-sizing(border-box);
  @include user-select(none);

  &.animating {
    @include transition(all 300ms $ui-animation-function);
  }

  &.panel-out {
    @include translate3d(-$ui-panel-width, 0, 0);
  }

  //shadow
  &:after {
    content: "";
    width: 5px;
    position: absolute;
    right: -5px;
    bottom: 0;
    top: 0;
    //Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#838e96+0,838e96+100&0.6+18,0+100
    background: -moz-linear-gradient(left, rgba(131, 142, 150, 0.3) 0%, rgba(131, 142, 150, 0.3) 18%, rgba(131, 142, 150, 0) 100%); // FF3.6-15
    background: -webkit-linear-gradient(left, rgba(131, 142, 150, 0.3) 0%, rgba(131, 142, 150, 0.3) 18%, rgba(131, 142, 150, 0) 100%); // Chrome10-25,Safari5.1-6
    background: linear-gradient(to right, rgba(131, 142, 150, 0.3) 0%, rgba(131, 142, 150, 0.3) 18%, rgba(131, 142, 150, 0) 100%); // W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+
    //filter: progid: DXImageTransform.Microsoft.gradient( startColorstr='#99838e96', endColorstr='#00838e96', GradientType=1); // IE6-9
  }

  .cloud-panel-header {
    background-color: lighten($ui-panel-bg, 2%);
    border-bottom: 1px solid $border-clr-light;
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    button {
      background: transparent;
    }
  }

  .cloud-panel-close {
    width: 35px;
    height: 35px;
    padding: 0;
    z-index: $ui-main-nav-index - 1;

    @include transition(background 500ms $ui-animation-function);

    i {
      font-size: 11px;
    }

    &:hover {
      cursor: pointer;
      color: darken($ui-txt-clr, 12%);
    }

    &:active {
      background-color: darken($ui-panel-bg, 20%);
    }
  }

  .cloud-panel-wrapper {
    flex-grow: 1;
    padding: 0 20px;

    .cloud-section {
      margin-bottom: 1em;
    }

    .cloud-content-list {
      > li {
        width: 33.333%;
        padding: 3px;

        .btn {
          padding-top: 10px;
          display: block;
          width: 100% !important;
          background-color: #fff;
        }
      }
    }
  }

  .cloud-panel-title {
    font-size: $font-size-base;
    padding-top: 20px;
    margin-bottom: 20px;
    text-transform: uppercase;
  }

  .cloud-panel-content {
    overflow-y: auto;
    -webkit-overflow-scrolling-y: touch;
    overflow-x: hidden;
  }

  p {
    margin-bottom: $cloud-spacer;
    font-size: $font-size-base;
  }

  .element_submit {
    display: none;
  }

  .cloud-panel-footer {
    position: absolute;
    bottom: 20px;
    left: 20px;
    right: 20px;

    .btn {
      margin-top: $cloud-spacer;
    }

    > .cloud-row {
      margin: 0;
    }
  }

  .new-page-list {
    margin-left: -$cloud-spacer;
    margin-right: -$cloud-spacer;
    display: flex;
    flex-wrap: wrap;

    li {
      padding: 0 $cloud-spacer;
      margin-bottom: $cloud-spacer * 2 !important;
      width: 33.33%;
    }
  }

  .cloud-list {
    list-style: none;

    li {
      margin-bottom: $cloud-spacer;
    }

    .btn {
      text-align: left;
    }
  }
}

.cloud-panel-fullpage {
  $fullpage-left: $ui-panel-min-width + $ui-main-nav-width;
  background-color: #fff;
  z-index: $ui-panel-index;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  width: calc(75vw - #{$ui-main-nav-width});
  max-width: calc(100vw - #{$fullpage-left});
  overflow: scroll;
  @include translate3d(0, 0, 0);
  opacity: 1;

  &.animating {
    @include transition(all 100ms $ui-animation-function);
  }

  &.panel-out {
    opacity: 0;
    @include translate3d(-$ui-panel-width, 0, 0);
  }

  p {
    margin-bottom: 16px;
    font-size: 16px;
  }

  .cloud-divider {
    margin-bottom: 20px;
  }

  .cloud-panel-wrapper {
    padding: 30px;
    margin: 0 auto;

    .cloud-panel-title {
      border-bottom: 1px solid $border-clr;
      font-size: 32px;
      font-weight: 600;
      margin-bottom: 0;
      padding-bottom: $cloud-spacer / 2;
    }

    .cloud-panel-section {
      font-size: 21px;
      margin-bottom: 0.8em;
    }
  }
}

//panel layer
.cloud-layer-panel-wrapper {
  @include box-sizing(border-box);
  padding: 0 20px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: $ui-panel-index + 1;

  @include translate3d(0, 0, 0);
  @include opacity(1);
  @include transition(all 300ms $ui-animation-function);

  &.panel-out {
    @include opacity(0);
    @include translate3d(-$ui-panel-width, 0, 0);
  }

  .cloud-panel-header {
    margin: 0 -20px;
  }

  .btn-close-layout {
    height: 35px;
    padding: 0 15px;
    text-align: left;

    i {
      @include translate3d(0, 0, 0);
      @include transition(all 300ms ease);
    }

    &:hover {
      i {
        @include translate3d(-4px, 0, 0);
      }
    }
  }

  .group.half-width {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    [class*='element_'] {
      width: 49%;
    }
  }
}

&.is-desktop {
  .cloud-panel {
    background-color: $ui-panel-bg;
    border-left: 1px solid $border-clr;
    position: fixed;
    left: $ui-main-nav-width;
    top: 0;
    bottom: 0;
    z-index: $ui-panel-index;
    width: 25vw;
    min-width: $ui-panel-min-width;

    &.cloud-hidden {
      display: block;
    }
  }

  .cloud-layer-panel-wrapper {
    background-color: $ui-panel-bg;
    width: 25vw;
    min-width: $ui-panel-min-width;
  }

  .small-screen & {
    .cloud-panel {
      left: $ui-main-nav-width * 3/4;
    }

    .cloud-panel-fullpage {
      $small-fullpage-left: $ui-panel-min-width + $ui-main-nav-width * 3/4;
      width: calc(75vw - #{$ui-main-nav-width * 3/4});
      max-width: calc(100vw - #{$small-fullpage-left});
    }

    #cloud-nav-secondary .btn {
      height: 2.5rem !important;
    }
  }
}

&.is-mobile {
  .cloud-panel {
    background-color: $ui-main-nav-bg;
    position: fixed;
    z-index: $ui-main-nav-index + 1;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;

    box-sizing: border-box;

    overflow: scroll;
    -webkit-overflow-scrolling: touch;

    input {
      font-size: 16px;
    }

    .cloud-panel-footer {
      background-color: $ui-main-nav-bg;
    }

  }

  .cloud-layer-panel-wrapper {
    background-color: $ui-main-nav-bg;
    right: 0;
  }
}
