 // Remove item added by LP Digital

.dd {
    position: relative;
    display: block;
    margin: 0;
    padding: 0;
    max-width: 600px;
    list-style: none;
    font-size: 13px;
    line-height: 20px;
}
  
.dd-list {
    display: block;
    position: relative;
    margin: 0;
    padding: 0;
    list-style: none;
}
  
.dd-list .dd-list {
    padding-left: 30px;
}
  
.dd-item, .dd-empty, .dd-placeholder {
    display: block;
    position: relative;
    margin: 0;
    padding: 0;
    min-height: 20px;
    font-size: 13px;
    line-height: 20px;

    .remove-item {
        float: right;
        padding: 5px;
        width: 25px;
        text-align: center;
        cursor: pointer;
    }
}
  
.dd-handle {
    display: block;
    height: 30px;
    padding: 5px 10px;
    color: #333;
    text-decoration: none;
    border: 1px solid #ccc;
    background: #fafafa;
    border-radius: 3px;
    box-sizing: border-box;
}
  
.dd-handle:hover {
    color: $cloud-brand;
    background: #fff;
    cursor: move;
}
  
.dd-item > button {
    position: relative;
    cursor: pointer;
    float: left;
    width: 25px;
    height: 20px;
    margin: 5px 0;
    padding: 0;
    text-indent: 100%;
    white-space: nowrap;
    overflow: hidden;
    border: 0;
    background: transparent;
    font-size: 12px;
    line-height: 1;
    text-align: center;
    font-weight: bold;
}
  
.dd-item > button:before {
    display: block;
    position: absolute;
    width: 100%;
    text-align: center;
    text-indent: 0;
}
  
.dd-item > button.dd-expand:before {
    content: '+';
}
  
.dd-item > button.dd-collapse:before {
    content: '-';
}
  
.dd-expand {
    display: none;
}

.dd-collapsed .dd-list, .dd-collapsed .dd-collapse {
    display: none;
}

.dd-collapsed .dd-expand {
    display: block;
}

.dd-empty, .dd-placeholder {
    margin: 5px 0;
    padding: 0;
    min-height: 30px;
    border: 1px dashed $cloud-brand;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
}

.dd-empty {
    border: 1px dashed #bbb;
    min-height: 100px;
    background-color: #e5e5e5;
    background-size: 60px 60px;
    background-position: 0 0, 30px 30px;
}

.dd-dragel {
    font-family: $font-family-default!important;
    position: absolute;
    pointer-events: none;
    z-index: 10999;

    li {
        font-family: $font-family-default!important;
        button {
            display: none;
        }
    }
}

.dd-dragel > .dd-item .dd-handle {
    margin-top: 0;
}

.dd-dragel .dd-handle {
    box-shadow: 2px 4px 6px 0 rgba(0,0,0,.1);
}

.dd-nochildren .dd-placeholder {
    display: none;
}

.dd-item {
    overflow: hidden;
    &:not(:first-child) {
        margin-top: $cloud-spacer;
    }

    &:hover .cloud-page-action{
        @include opacity(0);
        @include translate3d(100%, 0, 0);
    }

    .dd-item {
        margin-top: $cloud-spacer;
        &:hover .cloud-page-action{
            @include opacity(1);
            @include translate3d(0, 0, 0);
        }
    }
    
    .dd-content {
        display: block;
        height: 100%;
        overflow: hidden;
    }

    .cloud-page-action {
        top: 5px;
        bottom: auto;
        height: 30px;
        text-indent: initial;
    }

    .dd-handle {
        &:hover + .cloud-page-action {
            //&:extend(#cloud-ui .btn:hover > .cloud-page-action all);
        }

        .parent-node {
            padding-left: 18px;
        }
    }

    input {
        position: absolute;
        top: 11px;
        left: 30px;
    }
}