@import "ckeditor_skin.scss";
@import "ckeditor_dialog.scss";

.cke_panel_listItem {
    a {
        span {
            font-size: 12px!important;
        }
    }
}

.cke_button_icon {
    @extend .icon[class*=icon-];
}

.is-mobile {
    .cloud-cke {
        overflow-y: visible;
        width: 100%;
        right: 0;
        height: 40px;
        display: block!important;

        .confirm-rte {
            float:left;
            background-color: #F8F8F8;
            border: 1px solid #d1d1d1;
            border-right: 0;

            button {
                height: 36px;
                width: 36px;
                text-align: center;

                i {
                    font-size: 18px;
                }
            }
        }

        > .cke_inner {
            overflow-x: scroll;

            > .cke_top {
                width: 1200px;
                padding: 2px 8px 1px;
            }
        }

        > .cke_combopanel {
            width: 100% !important;
            top: auto !important;
            left: auto !important;
        }
    }
}

.footer-copyright {
    p.cloud-rte-enabled {
        min-width: 100px;
    }
}

//rte inline BO BackBee
.cloud-ui-wrapper {
    .cke_editable {
        @extend .cloud-control;
        margin-bottom: 10px;
        p, h1, h2, h3, .bigtext, .smalltext {
           &:last-child {
               margin-bottom: 0;
           }
        }
    }
    .input-wrapper .cke_editable {
        margin-bottom: 0px;

    }
}

