// Modal

.cloud-dialog {
    background-color: $modal-bg;
    position: fixed;
    top: 70%;
    left: 50%;
    min-width: 300px;
    z-index: $modal-index;
    box-shadow: 1px 1px 12px #555;
    outline: none;
    font-family: $font-family-default;
    color: $ui-txt-clr;
    font-size: $font-size-cloud;

    &.no-scroll {
        .ui-dialog-content {
            overflow: hidden;
        }
    }

    .ui-button {
        border: none;
        &:focus, &:active {
            outline: none;
            box-shadow: none;
        }
    }

    &:not(.with-btnClose) {
        .ui-widget-header .ui-dialog-titlebar-close {
            display: none;
        }
    }

    .ui-widget-header {
        font-family: $font-family-default;
        padding: $ui-dialog-spacer;
        padding-bottom: 0;
        cursor: grab;
        cursor: -moz-grab;
        cursor: -webkit-grab;
        color: #000;
        font-size: $font-size-large;
        border-top: $ui-dialog-border-top;

        .ui-dialog-title {
            color: $ui-txt-clr;

            .cloud-help-link{
                position: absolute;
                padding: 10px;
                top: 0;
                right: 0;
                margin: 20px 15px 0 0;
            }
        }

        .ui-dialog-titlebar-close {
            width: 32px;
            height: 32px;

            &:before, &:after {
                position: absolute;
                left: 15px;
                content: ' ';
                height: 22px;
                width: 2px;
                top: 6px;
                background-color: #555;
            }
            &:before {
                transform: rotate(45deg);
            }
            &:after {
                transform: rotate(-45deg);
            }

        }

        .ui-button {
            color: $ui-txt-clr;
            float: right;
            opacity: 1;
            &.ui-dialog-titlebar-close {
                position: absolute;
                top: 0;
                right: 0;
            }

            &:focus {
                outline: none;
            }

            .ui-button-text i {
                font-size: 20px;
            }
        }
    }

    .ui-dialog-content {
        background-color: #fff;
        padding: $ui-dialog-spacer;
        overflow: auto;
        font-size: 14px;
        .popin-content {
            .cloud-title {
                border-bottom: 0;
                margin-bottom: $cloud-spacer;
            }
            iframe {
                width: 100%;
            }
        }
    }
    .ui-dialog-buttonpane {
        background-color: $modal-footer-div;
        text-align: right;
        padding: $ui-dialog-spacer;
        padding-top: 0;
        overflow: hidden;

        .cloud-row {
            margin: 0;
            padding: 0;
            list-style: none;
            li {
                padding: 0;
                +li {
                    border-left:1px solid $modal-footer-div;
                }
            }
        }
        .ui-button {
            padding: 8px;
            background-color: $cloud-brand;
            margin-left: 5px;
            min-width: 100px;

            &.popin-ok,
            &:not(.popin-cancel) {
                color: #fff;
            }

            span.ui-button-text {
                color: #fff;
                text-transform: uppercase;
            }

            &:hover {
                background-color: rgba(red($cloud-brand), green($cloud-brand), blue($cloud-brand), 0.9);
            }

            @extend .btn;

            &.popin-cancel {
                background-color: transparent;
                span.ui-button-text {
                    color: #000;
                }
                &:hover {
                    background-color: $gray-lighter-plus;
                }

            }
        }
    }

    span.global-form-error {
        display: block;
        text-align: center;
        margin-bottom: 12px;
        color: $error-clr;
        padding: 8px;

        &:empty {
            display: none;
        }
    }

    span.form-error {
        display: block;
        margin-bottom: 2px;
        color: $error-clr;

        &:empty {
            display: none;
        }
    }

    &.account-popin {
        min-width: 165px;

        .ui-dialog-content.ui-widget-content {
            padding: 5px;

            .popin-content {
                div {
                    >.item:last-child {
                        border-bottom: none;
                    }
                }
                button {
                    background: transparent;
                }
            }
        }
    }

    &.remove-content {
        border-top: $ui-dialog-border-top;
    }

    &.remove-content {
        .popin-content {
            p {
                margin: 0;
            }
        }
    }

    &.cloud-dialog-mobile {
        top: 0!important;
        bottom: 0!important;
        left: 0!important;
        right: 0!important;
        width: auto!important;
    }

    &.modified-pages-list {
        .ui-dialog-content {
            background-color: transparent;
            min-height: 180px!important;
            max-height: none!important;
            position: absolute;
            top: 50%;
            left: 0;
            right: 0;
            margin-top: -158px;
        }

        .ui-dialog-buttonpane {
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
        }

        .message {
            font-size: 1em;
        }
    }

    &.logout {
        .popin-content {
            margin-top: 50px;
            text-align: center;
            p {
                margin-bottom: 50px;
                color: #fff;
                font-size: 22px;
            }
            i {
                font-size: 50px;
                color: #fff;
            }
        }
    }

    &.publish-pages-popover {
        .ui-dialog-content {
            background-color: transparent;
            min-height: 180px!important;
            max-height: none!important;
            position: absolute;
            top: 50%;
            left: 0;
            right: 0;
            margin-top: -158px;
        }

        .ui-dialog-buttonpane {
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
        }
    }

    // Pop up mobile
    &.auth-mobile {
        background-color: $cloud-brand;

        * {
            color: #fff;
        }

        .ui-dialog-title {
            color: #fff;
        }

        .ui-button-text i {
            color: #fff;
        }

        .ui-dialog-buttonpane {
            background: $cloud-brand;

            .ui-button {
                margin-bottom: $cloud-spacer;

                &:not(.popin-cancel) {
                    background: #fff;
                    .ui-button-text {
                        color: $cloud-brand;
                    }
                }

                &.popin-cancel .ui-button-text {
                    color: #fff;
                }
            }
        }

        .brand {
            text-align: center;
            margin-bottom: 35px;
            i {
                color: #fff;
                text-align: center;
                font-size: 5em;
                margin-bottom: 30px;
            }
        }

        .hauth{
            color: #fff;
            i{
                color: #fff;
            }
        }

        span.global-form-error {
            display: block;
            text-align: center;
            margin-bottom: 12px;
            color: #fff;
            background-color: rgba(255,255,255,0.2);
            padding: 8px;

            &:empty {
                display: none;
            }
        }

        .cloud-control {
            &:-webkit-autofill {
                -webkit-box-shadow: 0 0 0px 1000px $cloud-brand inset;
                -webkit-text-fill-color: #fff!important;
            }
            color: #fff;
            @include placeholder(#fff, italic);
        }

        .ui-widget-header .ui-button {
            color: #fff;
        }

        .ui-dialog-content {
            background-color: transparent;
            min-height: 180px!important;
            max-height: none!important;
            position: absolute;
            top: 50%;
            left: 0;
            right: 0;
            margin-top: -158px;

            .cloud-label {
                color: #fff;
            }
        }
        .ui-dialog-buttonpane {
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
        }

        .cloud-control {
            background-color: transparent;
        }

        .btn-primary {
            background-color: #fff;
            color: $cloud-brand;
            display: block;
            width: 100%;
            border-radius: 25px;
        }

        .cloud-forgot-password {
            a {
                color: #fff;
            }
        }

        .element_submit {
            .btn {
                margin: 20px 0;
            }
        }

        .btn.btn-primary {
            &:active {
                background-color: #fff;
                border-color: #fff;
                color: $cloud-brand;
            }
        }

        &.logout {
            max-width: 100%;
        }
    }

    &.cloud-dialog-popover {
        position: absolute;
        outline-width: 0;

        .ui-dialog-content {
            background-color:$modal-bg;
            color: $ui-txt-clr;
            margin: 0;
            padding: 20px;
            .popin-content {
                margin: 0;
                padding: 0!important;
                cursor: default;

                .modified-pages-link {
                    font-size: 10px;
                    color: $cloud-brand;
                    float: right;
                    text-transform: initial;
                }
            }
        }

        .ui-widget-header {
            display: none;
        }

        .arrow {
            position: absolute;
            bottom: -5px;
            left: 5px;
            height: 10px;
            width: 10px;
            background-color: #fff;

            z-index: -1;

            box-shadow: 4px 3px 15px 0px rgba(0, 0, 0, 0.2);

            @include transform(rotate(45deg));
        }

        &.cloud-dialog-popover-side-left {
            .arrow {
                left: -5px;
                bottom: 50%;
            }
        }

        &.cloud-dialog-popover-right {
            .arrow {
                left: auto;
                right: 5px;
            }
        }

    }

    &.help-center {
        padding-top: $ui-dialog-spacer;
        border-top: $ui-dialog-border-top;
        .ui-widget-header {
            display: none;
        }
    }

    .is-mobile & {
        max-width: 96%;
        left: 2%;
    }
}

//Modal
.cloud-modal {
    background-color: $modal-bg;
    position: fixed;
    top: 70%;
    left: 50%;
    min-width: 300px;
    box-shadow: 4px 3px 15px 0px rgba(0, 0, 0, 0.3);
    .cloud-header {
        text-transform: uppercase;
        padding: $ui-dialog-spacer;
        .close {
            color: $ui-txt-clr;
            font-size: 20px;
            opacity: 1;
        }
    }
    .cloud-modal-body {
        background-color: #fff;
        margin: $ui-dialog-spacer;
        margin-bottom: 30px;
        padding: 10px;
    }
    .cloud-footer {
        background-color: $modal-footer-div;
        .cloud-row {
            margin: 0;
            padding: 0;
            list-style: none;
            li {
                padding: 0;
                +li {
                    border-left: 1px solid $modal-footer-div;
                }
            }
        }
        .btn {
            background-color: #fff;
        }
    }
}
