// Cloud Elements

// Cloud spacer
.cloud-spacer {
    min-height: 20px;

    &.edit {
        background-color: $gray-lighter;
    }

    @include media-breakpoint-up(md) {
        &.bb-content:hover {
            background-color: $gray-lighter-plus;
        }

        &.cloud-resizing {
            background-color: $gray-lighter-plus;
        }
    }
}

.cloud-scroll > div {
    padding-left: 1px;
    padding-right: 1px;
}

// Pages offline
.offline {
    display: block;
    position: relative;

    &.menu-item, .submenu & {
        &:after {
            content: '*';
            color: $cloud-brand;
            position: absolute;
            right: -10px;
            top: 0;
        }
    }

    &.autoblock-item {
        padding-left: 10px;

        &:after {
            content: "*";
            color: $cloud-brand;
            position: absolute;
            left: -2px;
            top: 2px;
            font-size: 1rem;
        }
    }
}

.help-row-settings {
    text-align: right;
}

.row-settings-wrapper {
    text-transform: none;
    margin-top: 10px;
    form {
        ul.nav-tabs a {
            padding: 10px;
        }
    }
}