// Cloud - Font (Generate by ICOMOON)

.cloud-ico-plus {
    $btn-w: 16px;

    display: inline-block;
    width: $btn-w;
    height: $btn-w;
    position: relative;
    @include transition-delay(250ms);
    margin: 2px;

    i {
        display: block;
        position: absolute;
        width: 2px;
        height: 2px;
        border-radius: 50%;
        background-color: $cloud-brand;
        top: 50%;
        left: 50%;
        margin: -1px 0 0 -1px;
        @include transition-delay(400ms);

        &:before, &:after {
            content:"";
            position: absolute;
            left: 0;
            height: $btn-w/2;
            width: 2px;
            background-color: $cloud-brand;
            @include transition(400ms all linear);
        }

        &:before {
            top:0;
        }

        &:after {
            bottom:0;
        }
    }

    i + i {
        @include rotate(90deg);
    }

    .cloud-insert.fixed .btn-plugin & {

        i {
            background-color: transparent;

            &:before, &:after {
                height: 0;
                @include animation(icoInsert 400ms ease);
            }
        }

    }
}

@font-face {
  font-family: 'font-cloud';
  src:  url('#{$ico-path}/font-cloud.eot?jlgnq7');
  src:  url('#{$ico-path}/font-cloud.eot?jlgnq7#iefix') format('embedded-opentype'),
    url('#{$ico-path}/font-cloud.ttf?jlgnq7') format('truetype'),
    url('#{$ico-path}/font-cloud.woff?jlgnq7') format('woff'),
    url('#{$ico-path}/font-cloud.svg?jlgnq7#font-cloud') format('svg');
  font-weight: normal;
  font-style: normal;
}

.icon[class*=icon-] {
