// Cloud - Loader

@include keyframes(load) {
    0%   {
        @include rotate(0deg);
    }
    100% {
        @include rotate(360deg);
    }
}

.cloud-loading {
    .input-autocomplete & {
        width: 20px;
        height: 20px;

        &.center, &.top, &.bottom, &.bottom-sm, &.bottom-md, &.bottom-lg  {
            position: absolute;
            margin: -10px 0 0 -10px;
        }
    }

    $spacer: 30px;
    width: 30px;
    height: 30px;
    border-radius: 100%;
    border: 2px solid $cloud-brand;
    border-color: $cloud-brand $cloud-brand $border-clr $border-clr;
    margin: 0 auto;
    &.rotate {
      @include animation(load 1s infinite linear);
    }

    &.center, &.top, &.bottom, &.bottom-sm, &.bottom-md, &.bottom-lg  {
        position: absolute;
        margin: -15px 0 0 -15px;
    }

    &.center {
        top: 50%;
        left: 50%;
    }
    &.top {
        top: $spacer;
        left: 50%;
    }
    &.bottom {
        bottom: $spacer;
        left: 50%;
    }
    &.bottom-sm {
        bottom: $spacer * 0.5;
        left: 50%;
    }
    &.bottom-md {
        bottom: $spacer * 2;
        left: 50%;
    }
    &.bottom-lg {
        bottom: $spacer * 3;
        left: 50%;
    }
}
.cloud-loading-page {
    position:fixed;
    z-index: $ui-main-nav-index + 1;
    top: 50%;
    left: 50%;
    @include translate3d(-50%, -50%, 0);
    .cloud-loading {
        margin: -15px 0 0 -15px;
    }
}

.cloud-mask-loader {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: $ui-main-nav-index + 10;

    .global {
        padding: 25px 35px;
        background-color: rgba(red($gray-light), green($gray-light), blue($gray-light), 0.8);
        border-radius: 10px;

        .cloud-loading {
            margin: 0;
        }
    }
}