// Tags

.tag-list-container {
  .tag-list {
    .item-level {
      margin-right: 5px;
      margin-bottom: 5px;


      h1, h2, h3 {
        cursor: pointer;
      }
    }

    .item-level-1 {
      &:not(.has-children) {
        display: inline-block;
        vertical-align: top;
      }

      &.has-children {
        margin-bottom: 10px;
        h1 {
          border-bottom: 1px solid #ddd;
        }
      }

      h1 {
        font-size: 1.2rem;
        text-transform: uppercase;
        font-weight: bold;
        color: #222;
        padding-bottom: 5px;
        margin-bottom: 5px;
      }

      .item-level-2 {
        margin-right: 5px;
        display: inline-grid;

        h2 {
          font-size: 1rem;
          text-transform: uppercase;
        }


        .item-level-3 {
          margin-top: 10px;
          h3 {
            font-size: 0.875rem;
            color: #888;
          }
        }
      }
    }

  }
}

.cloud-tag {
  align-items: center;
  border: 1px solid darken($border-clr, 10%);
  display: inline-flex;
  font-size: $font-size-cloud;
  padding: 5px 10px;
  margin: 0 0.8*$spacer-x 0.8*$spacer-x 0;
  position: relative;

  span {
    vertical-align: middle;
  }

  .btn {
    padding: 6px;

    .icon {
      margin: 0;
      font-size: 15px;
    }
  }

  .more-options {
    .btn .icon {
      color: $white;
    }
  }

  .cloud-tag-param {
    float: right;

    & + button.ok {
      margin-left: 5px;
    }

    .btn-more {
      i {
        @include rotate(90deg);
      }

      &.active {
        background: #ddd;
        border-color: #ddd;
      }
    }

    .more-options {
      background: $cloud-brand;
      position: absolute;
      right: 0;
      top: 32px;
      display: inline-flex;
      flex-direction: column;
      z-index: 1;
      opacity: 0;
      visibility: hidden;
      @include transition(all linear 100ms);

      &.show {
        opacity: 1;
        visibility: visible;
      }
    }
  }
}

.tag-level {
  .cloud-tag-param {
    float: right;
    position: relative;

    .btn-more {
      padding: 0 5px;

      i {
        @include rotate(90deg);
        margin: 0;
      }

      &.active {
        background: #ddd;
        border-color: #ddd;
      }
    }

    .more-options {
      background: #fff;
      border: 1px solid #ddd;
      position: absolute;
      left: 0;
      top: 24px;
      display: inline-flex;
      flex-direction: column;
      z-index: 1;
      opacity: 0;
      visibility: hidden;
      @include transition(all linear 100ms);

      .btn {
        color: $cloud-brand;
      }

      &.show {
        opacity: 1;
        visibility: visible;
      }
    }
  }
}

.overlay-items {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.tags-wrapper {
  span.loader {
    display: block;
    height: 30px;
  }
}

.create-tag {
  margin-bottom: .5em;

  button {
    margin-top: .5em;
  }
}