// Cloud Placeholder - Missing Config Bloc Placeholder & Placeholder Image

.missing-config-wrapper {
    font-family: $font-family-default;
    border: 1px dashed $border-clr;
    padding: 16px;

    .missing-config {
        background-color: $gray-lighter-plus;
        color: $ui-txt-clr;
        padding: 32px 16px;
        overflow: hidden;

        i.icon.icon-info {
            display: block;
            font-size: 24px;
            height: 38px;
            line-height: 38px;
            text-align: center;
            width: 38px;
            float: left;
            margin: 0 10px 10px 0;
            color: $ui-txt-clr;
        }
        & p {
            line-height: 1.5em;
            margin: 0;
            padding: 0; 
            max-width: 77%;
            line-height: 38px;
            float: left;
            font-size: 1rem;
            * {
                color: $ui-txt-clr;
            }

            i.icon.icon-setting {
                color: $cloud-brand;
            }
        }
    }
}

.cloud-placeholder {
    font-family: $font-family-default;
    background-color: $gray-lighter-plus;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    pointer-events: none;

    &:after {
        content: "";
        display: block;
        padding-top: 62.714%;
    }
    .cloud-placeholder-content {
        position: absolute;
        top: 50%;
        left: 0;
        right: 0;
        @include translate3d(0, -50%, 0);
        text-align: center;
        
        &, & * {
            color: $ui-txt-clr;
        }
        .ico {
            display: block;
            background-color: $border-clr;
            border-radius: 100%;
            width: 60px;
            height: 60px;
            font-size: 25px;
            line-height: 60px;
            margin: 0 auto 2px auto;
            cursor: pointer;
        }

        span.label {
            font-family: $font-family-default;
            text-transform: uppercase;
            font-size: $font-size-small;
            line-height: 1;
            white-space: normal;
        }
    }

    &-droppable {
        margin: -10px;
        padding: 10px;
        border: 1px dashed $cloud-brand;
    }

    @media(min-width: 1025px) {
        &-over:hover &, &-droppable & {
            z-index: auto;
            opacity: 1;
        }
    }

    .cloud-logo & {
        min-width: 130px;
        left: auto;
        right: auto;

        div {
            .ico {
                width: 40px;
                height: 40px;
                line-height: 45px;
                font-size: 18px;
            }
        }
    }
    .footer & {
        position: relative;
        max-width: 220px;
        margin: 0 auto;
    }
}

.has-image-placeholder {
    &.bb-content.cloud-logo {
        position: relative;
        height: 58px;
        min-width: 130px;
        @include media-breakpoint-up(sm) {
            height: 100px;
        }
    }
    &.figure {
        &.image-resize, &.image-fullwidth {
            padding-top: 50%;
            &:after {
                display: none;
            }
        }
    }
}

.cloud-text-placeholder:before {
    content: attr(data-cloud-placeholder);
}