// Cloud edit content & Row setting

.mandatory-fields {
    border: 3px solid red;
}

.btn-plugin {
    height: $btn-plugin-w;
    width: $btn-plugin-w;
    border: 1px solid transparent;
    box-shadow: 0px 0 4px 1px rgba(50, 50, 50, 0.3);
    border-radius: 100%;
    background-color: #fff;
    position: relative;
    padding: 7px;
    outline-width: 0;
    line-height: 1;
    @include transition(all $ui-transition);

    .icon {
        color: $cloud-brand !important;
        font-size: 19px;
    }
    span {
        display: none;
        opacity: 0;
        white-space: nowrap;
        font-size: $font-size-small;
        color: $gray-light;
        position: absolute;
        top: 0;
        left: 50%;
        padding: 0 $btn-plugin-w/2 + 3;
        text-transform: uppercase;

        &.force {
            display: block;
            opacity: 1;
            top: unset;
            bottom: 0;
            font-size: $font-size-extra-small;
        }
    }
    &:hover {
        background-color: $btn-plugin-bg-hover;
        span {
            display: block;
            opacity: 1;
            line-height: $btn-plugin-w;
        }
    }
    &:active {
        color: #fff;
        background-color: $cloud-brand;
        .icon {
            color: #fff;
        }
    }
    &.btn-add-content {
        .icon {
            font-size: 15px;
        }
        padding: 7px 8px 7px 7px;
    }

    button {
        &:focus, &:active {
            outline: none;
            box-shadow: none;
        }
    }
}


// Has content child block parameter (ref. Card)
.has-sub-elements {
    .cloud-edit-content {

        .cloud-left {
            &.is-in-content {
                top: auto;
                left: auto;
                right: 0;
                min-width: auto;
            }

            .more-options {
                z-index: -1;
            }

            .btn-more:hover {
                & + .parameters-label {
                    display: block;
                }
            }

            .parameters-label {
                display: none;
                position: absolute;
                white-space: nowrap;
                top: 0;
                z-index: -1;
                background: #c31b1b;
                right: 17px;
                line-height: 35px;
                left: auto;
                padding: 0px 24px 0 10px;
                background-color: rgba(255, 255, 255, 0.6);
                font-size: $font-size-extra-small;
                text-transform: uppercase;
                color: $gray;
            }
        }
    }
 }

.btn-row-settings {
    position: absolute;
    width: 100%;
    top: 0;
    right: 0;

    .row-settings {
        position: absolute;
        right: 10px;
        top: 10px;
        text-align: center;
        z-index: $dnd-index + 1;

        .icon {
            color: $gray;
            font-size: 19px;
        }

        &:active {
            .icon {
                text-decoration: none;
            }
        }

        &:hover {
            & + span {
                display: block;
            }
        }
    }

    span {
        display: none;
        position: absolute;
        right: 28px;
        top: 10px;
        line-height: $btn-plugin-w;
        left: auto;
        padding: 0px 26px 0 10px;
        background-color: rgba(255, 255, 255, 0.6);
        z-index: $dnd-index;
        font-size: $font-size-base;
        text-transform: uppercase;
        color: $gray;
        @include transition(opacity linear 200ms);
    }
}

.highlight-content {
    z-index: $overlay-index + 10;
    @include box-shadow(1px 1px 12px #555);
}

body:not(.cloud-dragging)  {
    .cloud-dnd-content {
       display:none;
    }

    .cloud-editable, .cloud-rte-enabled {
        position: relative;
        display: flow-root;
        box-shadow: 0px 0px 0px 1px $border-clr;
        @include animation(bbContentEditable 0.2s);
    }

    .cloud-rte-enabled {
        min-height: 26px;

        & + .cloud-edit-content {
            display: none;
        }
    }
}

body.cloud-dragging {
    .cloud-dnd-content {
        z-index: 50;
    }

    .cloud-edit-content {
        display: none;
    }
}

.cloud-editable.cloud-rte-enabled {
    .cloud-edit-content {
        display: none;
    }
}

.navbar .cloud-rte-enabled {
    min-width: 50px;
}
.resize-wrapper {
    border: 1px solid $gray-lighter-plus;
}

.col-marge-dnd-left {
    left: 15px;
}

.col-marge-dnd-right {
    right: 15px;
}

.colcontentset, .ghost-content {
    &.ghost {
        border: 1px dashed $gray-lighter;
    }
}

.cloud-bottom-button {
    position: fixed;
    bottom: 20px;
    text-align: center;
    padding-left: $spacer-x/2;
    padding-right: $spacer-x/2;
    width: 100%;
    z-index: $ui-main-nav-index;

    .btn {
        @extend .btn-plugin;
        padding-left: 3em;
        padding-right: 3em;
        width: 100%;
    }
}

.cloud-rte, .cloud-rte-mobile {
    $padding : 10px;

    margin-left: -$padding;
    margin-right: -$padding;
    position: relative;
    .medium-editor-toolbar .medium-editor-toolbar-form {
        &.medium-editor-toolbar-form-active {
            display: block;
        }
    }
}

.cloud-rte-mobile {
    position:absolute;
    top:0;
    left: 0;
    right: 0;
    z-index: $ui-main-nav-index;
    opacity: 1;
    padding: 0;
    margin: 0;
    @include transition(opacity linear 200ms);

    &.cloud-hidden {
        display: block;
        opacity: 0;
    }
}

.cloud-image-upload, .cloud-image-rsz {
    .btn-plugin {
        span {
            right: 50%;
            left: auto;
        }

        .icon {
            color: $gray;
        }

    }

    .ui-draggable-dragging &, .cloud-dragging & {
        display: none;
    }
}

.cloud-image-resize, .cloud-resize, .cloud-image-height-info {
    border-radius: 6px 6px 0 0;
    border: 1px solid $border-clr;
    position: absolute;
    background-color: #fff;
    width: 30px;
    margin-left: -15px;
    text-align: center;

    i {
        color: $cloud-brand !important;
    }

    .bg & .icon {
        color: $cloud-brand;
    }

    .has-image-placeholder & {
        display: none;
    }
}

.cloud-image-height-info {
    color: $ui-txt-clr !important;
    width: auto;
    padding-left: 10px;
    padding-right: 10px;
    font-family: $font-family-default;
    z-index: 1;
}

.cloud-guideline {
    position: absolute;
    height: 1px;
    width: 100vw;
    background-color: $cloud-brand;
    z-index: 1;
}

.cloud-edit-content {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: $dnd-index + 91;
    text-align: left;
    box-shadow: none;
    @include animation(pluginFadeIn 0.2s);

    .cloud-resizing & {
        display: none;
    }

    .cloud-left, .cloud-right {
        position: absolute;
        top: -18px;
        left: -18px;
        min-width: 40px;

        .ui-draggable-dragging &, .cloud-dragging & {
            display: none;
        }

        &.is-in-content {
            top: 0px;
            left: 0px;
        }
    }

    .cloud-insert & .cloud-left {
        min-width: 0;

        .btn-plugin:hover span {
            line-height: inherit;
        }
    }

    .cloud-left {
        z-index: 1;
        .more-options {
            position: absolute;
            top: -10px;
            left: -22px;
            opacity: 0;
            @include transition(all 250ms ease);
            .btn-plugin {
                margin-bottom: 5px;
                position: absolute;
                top: 10px;
                left: 22px;
                @include transition(all 250ms ease);
            }
        }

        &:hover {
            & > .more-options {
                height: 14em;
                width: 5em;
                opacity: 1;

                & > .btn-plugin {
                    $btn-plugin-top: 50px;
                    $btn-plugin-interval: 40px;
                    &:nth-child(1) {
                        top: $btn-plugin-top;
                    }
                    &:nth-child(2) {
                        top: $btn-plugin-top + $btn-plugin-interval;
                    }
                    &:nth-child(3) {
                        top: $btn-plugin-top + $btn-plugin-interval * 2;
                    }
                    &:nth-child(4) {
                        top: $btn-plugin-top + $btn-plugin-interval * 3;
                    }
                }
            }

            & > .btn-more {
                color: #fff;
                background-color: $cloud-brand;
                border-color: $cloud-brand;
                .icon {
                    color: #fff !important;
                }
            }

        }
    }

    .cloud-right {
        left: auto;
        right: -12px;
        text-align: right;
        .btn-plugin {
            span {
                right: 50%;
                left: auto;
            }
        }

        &.is-in-content {
            right: 0px;
            left: auto;
        }
    }

    .cloud-center .btn-cloud-label {
        background-color: #fff;
        color: $ui-txt-clr !important;
        display: block;
        height: 22px;
        border: 1px solid $border-clr;
        border-radius: 6px 6px 0 0;
        font-size: 11px;
        font-family: $font-family-default;
        font-weight: 700;
        left: 50%;
        line-height: 20px;
        margin: 0 0 0 -2 * $btn-plugin-w;
        position: absolute;
        text-align: center;
        text-transform: uppercase;
        top: -22px;
        width: 4 * $btn-plugin-w;
    }

    .btn-cloud-grip {

        &.small {
            width: 2 * $btn-plugin-w;
            margin: -1px 0 0 -1 * $btn-plugin-w;
        }

        cursor: grab;
            cursor: -moz-grab;
            cursor: -webkit-grab;
        background-color: #fff;
        display: block;
        height: 0.6 * $btn-plugin-w;
        width: 4 * $btn-plugin-w;
        border: 1px solid $border-clr;
        position: absolute;
        left: 50%;
        margin: -1px 0 0 -2 * $btn-plugin-w;
        border-radius: 0 0 6px 6px;
        text-align: center;
        line-height: 14px;
        @include transition(all 250ms ease);

        &:after {
            content: "";
            display: inline-block;
            width: 2px;
            height: 2px;
            background-color: $cloud-brand;
            vertical-align: middle;
            margin-top: -2px;
            box-shadow: 5px 0 0 $cloud-brand, 10px 0 0 $cloud-brand, -5px 0 0 $cloud-brand, -10px 0 0 $cloud-brand, 0 4px 0 $cloud-brand, 5px 4px 0 $cloud-brand, -5px 4px 0 $cloud-brand, 10px 4px 0 $cloud-brand, -10px 4px 0 $cloud-brand;
        }

        &:hover {
            background-color: $btn-plugin-bg-hover;
        }

        .ui-draggable-dragging &, .cloud-dragging & {
            display: none;
        }

        @media screen {
            cursor: move!important;
        }

    }

    .not-set-block:hover & {
        .cloud-left {
            .more-options {
                height: 14em;
                width: 5em;
                opacity: 1;
                & > .btn-plugin {
                    &:nth-child(1) {
                        top: 50px;
                    }
                    &:nth-child(2) {
                        top: 90px;
                    }
                    &:nth-child(3) {
                        top: 130px;
                    }
                    &:nth-child(4) {
                        top: 170px;
                    }
                }

                .btn-parameter {
                    animation: zoominout 1s infinite ease-in-out;
                    @keyframes zoominout{
                        0%{
                            transform: scale(1) rotate(0deg);
                        }
                        50%{
                            transform: scale(1.1) rotate(0.1deg);
                        }
                        100%{
                            transform: scale(1) rotate(0deg);
                        }
                    }
                }

            }
        }
    }
}

// When the "display on hover" option active, always show rte during editing
.lead.hover-display.cloud-rte-enabled {
    opacity: 1 !important;
}

// DND
body.cloud-dragging {
    cursor: move !important;
    .cloud-edit-content .btn-cloud-grip, & {
        cursor: grabbing!important;
        cursor: -moz-grabbing!important;
        cursor: -webkit-grabbing!important;
    }
}

// Resize colomn
.resize-wrapper-element {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: $ui-main-nav-index + 10;
}

// Menu Parameter
li.menu-add-parameter {
    display: none;

    i {
        display: inline-block;
        width: 32px;
        height: 32px;
        line-height: 30px !important;
        background: #fff;
        text-align: center;
        border-radius: 100%;
        color: $cloud-brand !important;
        font-size: 18px;
        box-shadow: 0px 0 4px 1px rgba(50, 50, 50, 0.3);
    }
    .nav-burger & {
        position: absolute;
        z-index: 1;
    }

    .navbar-expand-lg .navbar-collapse:hover .navbar-nav:not(.ml-auto) & {
        position: absolute;
        z-index: 1;
        left: 0;
    }
}

// Footer (menu parameter)
.navbar-collapse:hover, .footer .navbar-expand:hover {
    .menu-add-parameter {
        display: list-item;
        position: absolute;
        .navbar-expand-lg & {
            @include media-breakpoint-up(lg) {
                position: relative;
            }
        }
    }
}

@include media-breakpoint-down(md) {
    .navbar-expand-lg li.menu-add-parameter {
        display: none;
    }
}

.footer {
    .bb-content {
        position: relative;
        .menu-add-parameter {
            position: absolute;
            left: 0;
        }
    }
}
