// Button

.btn {
    border: 1px solid transparent;
    display: inline-block;
    font-size: 13px;
    line-height: 18px;
    padding: 9px;
    position: relative;
    outline: none;
    cursor: pointer;
    white-space: nowrap;
    @include box-sizing(border-box);
    @include transition(all 250ms ease);

    .btn.cloud-page-action {
        background: $cloud-brand;
        bottom: 0;
        color: #fff;
        margin: 0;
        padding: 0 10px 0 20px;
        position: absolute;
        right: 0px;
        top: 0px;
        width: auto;

        .icon {
            vertical-align: middle;
        }

        opacity: 0;
        @include translate3d(100%, 0, 0);

        @include transition(all linear 100ms);

        &:hover {
            @include translate3d(0, 0, 0);
        }
    }

    .icon {
        display: inline-block;
        text-decoration: none;
        margin: 0 5px;

        &:not(:last-child) {
            margin-right: 2px;
        }
    }
    .icon-right {
        float: right;
        text-align: right;
    }

    &.cloud-hidden {
        display: none;
    }

    &.is-drafted {
        background-color: $cloud-brand!important;
    }

    &.button-transparent {
        padding: 4px 6px;
        height: 32px;
        i {
            font-size: 18px;
            width: 1em;
            line-height: 22px;
            margin: 0;
        }
    }

    &.btn-min-width {
        min-width: 120px;
    }

    .cloud-label {
        white-space: normal;
        margin: 0;
    }

    &.btn-primary {
        background-color: $cloud-brand;
        border-color: $cloud-brand;
        color: $white;
    }

    &.btn-outline-primary {
        border-color: $cloud-brand;
        background: transparent;
        color: $cloud-brand;

        &:hover {
            border-color: $cloud-brand;
            background: $cloud-brand;
            color: $white;
        }
    }

    &.active {
        border-color: $cloud-brand;
    }

    &[disabled] {
        border-color: $btn-disabled-border;
        background-color: $btn-disabled-bg;
        cursor: not-allowed;
    }

    &:focus, &:active {
        outline: none;
    }

    &.btn-xs {
        font-size: 11px;
        padding: 10px;
    }

    &.btn-layout {
        width: 52px;
        padding-left: 0;
        padding-right: 0;
        text-align: center;
        .icon {
            width: auto;
        }
    }

    &:hover {
        .cloud-page-status {
            opacity: 0;
        }
        > .cloud-page-action {
            opacity: 1;
        }
    }

    &.btn-publish {
        background-color: $btn-publish-bg;
        color: $btn-publish-clr;
        span {
            color: $btn-publish-clr !important;
        }

        i {
            color: #fff;
        }

    }

    &.btn-sm {
        padding: 7px;
        font-size: 11px;
        line-height: 12px;
    }

    //button with icon
    &.btn-icon-xl i.icon {
        font-size: 2em;
        width: 1em;
    }

    &.btn-icon-md {
        line-height: 15px;
    }

    &.btn-icon-sm {
        padding: 6px .5em;
        i.icon {
            margin: 0 2px;
        }
    }

    &[data-value^='color'] {
        padding: 15px;
    }
}

.item-sm {
    .btn {
        padding-top: 5px;
        padding-bottom: 5px;

        .cloud-page-action {
            top: 0px;
        }
    }

    button.btn {
        i {
            margin: 0!important;
        }
    }

    .cloud-row {
        margin: 0;
    }
}

.btn-add-item {
    background-color: #fff;
    border: 1px solid $border-clr;
    text-transform: uppercase;
    padding: 12px;

    &:hover {
        background-color: $gray-lighter-plus;
    }
    &.selected {
        border: 1px solid $cloud-brand;
        @include box-shadow(0 0 0 1px $cloud-brand);
    }
}

.btn-default {
    background-color: #fff;
    border: 1px solid $border-clr;
    text-transform: uppercase;
    &:hover {
        background-color: $gray-lighter-plus;
    }
    &.selected {
        border: 1px solid $cloud-brand;
        @include box-shadow(0 0 0 1px $cloud-brand);
    }
    &.btn-disabled{
        background-color: $gray-lighter-plus;
    }
}

.btn-small {
    //&:extend(#cloud-ui .btn-default all);
    padding: 8px;
    text-transform: initial;
}

.btn-danger {
    background-color: $error-clr;
    border: 1px solid $error-clr;
    text-transform: uppercase;
    &:hover, &:active {
        background-color: lighten($error-clr, 5%);
    }
    &.selected {
        background-color: $error-clr;
    }
}

&.desktop {
    .btn-publish {
        &:hover {
            background-color: darken($btn-publish-bg, 5%)!important;
        }
    }
}

.btn-icon {
    .icon {
        margin: 0;
        font-size: 15px;
    }
}
.btn-block {
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    padding: 15px;
}
.btn-account {
    width: 100%;
    height: 35px;
    font-size: 12px;
    padding: 7px 10px;
    text-align: left;
    text-transform: initial;
    border-bottom: 1px solid #bcbcbc;
}

.langbo {
    width: 100%;
    padding-top: 6px;
    padding-bottom: 6px;
    padding-left: 10px;
    font-size: 12px;
    text-transform: initial;
    color: #000;
    border-bottom: 1px solid #bcbcbc;
    display: flex;
    align-items: center;

    &.mobile {
        background-color: #fff;
        border: 1px solid $border-clr;
        color: $gray;
        font-size: 13px;
        padding: 11px;
        text-transform: uppercase;
    }

    .btn {
        padding: 3px 8px;
        font-size: 10px;
        margin-left: 3px;
        color: $cloud-brand;
        border-radius: 2px;
        text-align: center;
        border-color: $cloud-brand;
        background: transparent;

        &.active {
            background: $cloud-brand !important;
            color: $white;
        }
    }
}

//
.txt-primary {
    color: $cloud-brand;
}
