// Cloud - Popover

.popover-wrapper {
    position: relative;
}

.cloud-popover {
    &.popover {

        background-color:$popover-bg;
        border-radius: 0;
        display:none;
        opacity: 0;
        z-index: $modal-index;

        @extend .cloud-dialog;

        &.show {
        opacity: 1;
        display: block!important;
        }

        &.popover-right {
        margin-left: 5px;
        }

        &.popover-left {
        margin-left: -5px;
        }

        &.popover-bottom {
        margin-top: 5px;
        }

        &.popover-top {
        margin-top: -5px;
        }

        .popover-content {
        padding: 15px;
        p {
            //&:extend(#cloud-ui .bb5-ui-reset);
            font-size: $font-size-base;
            margin: 0 0 1em 0;
        }
        }

        /* Arrow */

        .popover-arrow,
        .popover-arrow:after {
        position: absolute;
        display: none;//block
        width: 0;
        height: 0;
        border-color: transparent;
        border-style: solid;
        }

        .popover-arrow {
        border-width: 11px;
        }

        &.popover-bottom > .popover-arrow {
        left: 50%;
        margin-left: -11px;
        border-top-width: 0;
        border-bottom-color: $popover-border;
        top: -11px;
        }

        &.popover-top > .popover-arrow {
        left: 50%;
        margin-left: -11px;
        border-bottom-width: 0;
        border-top-color: $popover-border;
        bottom: -11px;
        }

        &.popover-right > .popover-arrow {
        top: 50%;
        left: -11px;
        margin-top: -11px;
        border-left-width: 0;
        border-right-color: $popover-border;
        }

        &.popover-left > .popover-arrow {
        top: 50%;
        right: -11px;
        margin-top: -11px;
        border-right-width: 0;
        border-left-color: $popover-border;
        }

        .popover-arrow:after {
        border-width: 10px;
        content: "";
        }

        /* direction specific */
        &.popover-bottom > .popover-arrow:after {
        content: " ";
        top: 1px;
        margin-left: -10px;
        border-top-width: 0;
        border-bottom-color: $popover-bg;
        }

        &.popover-top > .popover-arrow:after {
        content: " ";
        bottom: 1px;
        margin-left: -10px;
        border-bottom-width: 0;
        border-top-color: $popover-bg;
        }

        &.popover-right > .popover-arrow:after {
        content: " ";
        left: 1px;
        bottom: -10px;
        border-left-width: 0;
        border-right-color: $popover-bg;
        }

        &.popover-left > .popover-arrow:after {
        content: " ";
        right: 1px;
        bottom: -10px;
        border-right-width: 0;
        border-left-color: $popover-bg;
        }

    }
}