// Linear gradient
@mixin linear-gradient($from, $to, $stop: 100%) {
  background-color: $from;
  background-image: -moz-linear-gradient(left, $from 0%, $to $stop);
  background-image: -webkit-gradient(linear, left top, right top, color-stop(0%, $from), color-stop($stop, $to));
  background-image: -webkit-linear-gradient(left, $from 0%, $to $stop);
  background-image: -o-linear-gradient(left, $from 0%, $to $stop);
  background: -ms-linear-gradient(left, $from 0%, $to $stop);
  background: linear-gradient(to right, $from 0%, $to $stop);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='$from', endColorstr='$to',GradientType=1 );
}

// Blur
@mixin blur($blur){
  filter: blur($blur);
  -moz-filter: blur($blur);
  -webkit-filter: blur($blur);
  -o-filter: blur($blur);
}

// Image svg
@mixin image-svg($image) {
  background-image: url('#{$img-path}/#{$image}.svg');
}

// Animation
@mixin keyframes($keyframes) {
  @-webkit-keyframes #{$keyframes} {
    @content; 
  }
  @-moz-keyframes #{$keyframes} {
    @content;
  }
  @-ms-keyframes #{$keyframes} {
    @content;
  }
  @-o-keyframes #{$keyframes} {
    @content;
  }
  @keyframes #{$keyframes} {
    @content;
  } 
}

// New content fade in
@include keyframes(cloudHeighlight) {
  0% {
    opacity: 0;
    outline: 2px solid $cloud-brand;
  }
  100% {
    outline-color: transparent;
    opacity: 1;
  }
}

//BG color overlay
@include keyframes(overlayFadeIn) {
  0% {
    background-color: rgba(red($overlay-color), green($overlay-color), blue($overlay-color), 0);
  }
  100% {
    background-color: rgba(red($overlay-color), green($overlay-color), blue($overlay-color), 0.54);
  }
}

/////////////////////////////////////////////////
// bbContent over
@include keyframes(bbContentEditable) {
  0%   {
    box-shadow: 0px 0px 0px 1px transparent;
  }
  100% {
    box-shadow: 0px 0px 0px 1px $border-clr;
  }
}

/////////////////////////////////////////////////
// bbPlugin fade in
@include keyframes(pluginFadeIn) {
  0%   {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

/////////////////////////////////////////////////
// Ico insert
@include keyframes(icoInsert) {
  0%   {
    height: 10px;
  }
  50% {
    height: 5px;
  }
  100% {
    height: 0;
  }
}