// Cloud FormBuilder - Image List

// Image List
.image-list {
    @extend .clearfix;
    .item {
        float: left;
        width: 50%;

        &:hover {
            cursor: pointer;
        }

        .figure {
            background-size: cover;
            background-repeat: no-repeat;
            background-position: 50% 50%;
            height: 120px;
            position: relative;
            margin: 5px;

            &:hover {
                > .cloud-edit-content {
                    opacity: 1;
                    display: block;
                }
            }

            .cloud-edit-content {
                opacity: 0;
                display: none;
                left: auto;
                @include transition(all 100ms $ui-animation-function);

                .btn-plugin {
                    background-color: $gray;
                    color: #fff;
                    text-align: center;
                    display: block;
                    font-size: 18px;

                    i {
                        color: #fff !important;
                        margin-left: 0;
                        margin-right: 0;
                    }

                    &:hover > span {
                        left: auto;
                        right: 25px;
                        background: rgba(85, 85, 85, .5);
                        color: #fff;
                    }
                }
            }
        }
    }

    .cloud-dropzone {
        float:left;
        background: white;
        width: 50%;
    }
}
