// Cloud text list

// Action button

.cloud-page-action {
    background: $cloud-brand;
    bottom: 0;
    color: #fff;
    margin: 0;
    padding: 0 $cloud-spacer 0 $cloud-spacer * 2;
    position: absolute;
    right: 0px;
    top: 0px;
    width: auto;
    @include opacity(0);
    @include translate3d(100%, 0, 0);
    @include transition(all linear 100ms);

    &:hover {
        right: 0px!important;
        @include translate3d(0, 0, 0);
    }
    &.remove {
        &:before, &:after {
            background-color: #fff;
            content: "";
            position: absolute;
            top: 50%;
            bottom: 0;
            width: 11px;
            height: 1px;
            left: 6px;
        }

        &:before {
            @include rotate(45deg);
        }

        &:after {
            @include rotate(-45deg);
        }
    }
}

.item:hover, .dd-item:hover {
    .cloud-page-action {
        @include translate3d(0, 0, 0);
        @include opacity(1);
    }
}
