//
// Cloud UI SASS variables Cloud UI

// out: null, main: cloud.less

$gray-base:                 #080808;
$gray-darker:               lighten($gray-base, 13.5%); // #222
$gray-dark:                 lighten($gray-base, 20%);   // #333
$gray:                      lighten($gray-base, 33.5%); // #555
$gray-light:                lighten($gray-base, 46.7%); // #777
$gray-lighter:              lighten($gray-base, 60.0%); // #999
$gray-lighter-plus:         lighten($gray-base, 91.8%); // #eee

//Img
$img-path:                  '../img';

//Font
$font-family-default:       'Open Sans', Arial, Helvetica, sans-serif;


//Icon
$ico-path:                  '../fonts/cloud';
$ico-font:                  'font-cloud';
$cloud-var-check:           '\e92d';


//Grid & space
$spacer-y:                  10px;
$spacer-x:                  10px;


//Color
$cloud-brand:               #D94717;
$ui-txt-clr:                #555;
$border-clr:                #c5c5c5;
$border-clr-light:          #ddd;
$info-clr:                  #4FC7D8;
$success-clr:               #2BAC75;
$warning-clr:               #FCB040;
$error-clr:                 #FF7373;
$progress-cursor-clr:       #DA4A1A;
$progress-bg-clr:           #ccc;

//Buttons
$btn-publish-clr:           #fff;
$btn-publish-bg:            #888;
$btn-plugin-bg-hover:       #f2f2f2;
$btn-plugin-w:              36px;
$btn-disabled-border:       #ddd;
$btn-disabled-bg:           #f2f2f2;


//Main nav
$ui-main-nav-width:         80px;
$ui-main-nav-bg:            #f2f2f2;
$ui-main-nav-index:         10000;


//Panel
$ui-panel-index:            $ui-main-nav-index - 1;
$ui-panel-width:            420px;
$ui-panel-min-width:        350px;
$ui-panel-bg:               #eee;
$ui-animation-function:     cubic-bezier(0.35, 0.63, 0.47, 0.91);


//Form
$control-clr:               $ui-txt-clr;
$control-bg:                #fff;


//Editor
$editor-bg:                 #3e3e3e;
$editor-clr:                #ccc;
$editor-btn-active:         #797979;

//Dnd
$dnd-index:                 $ui-main-nav-index - 100;

//Insert
$insert-index:              $dnd-index + 10;

//Modal
$modal-bg:                  #fff;
$modal-footer-div:          #fff;
$modal-index:               $ui-main-nav-index + 1;

//Popover
$popover-bg:                $modal-bg;
$popover-border:            rgba(0,0,0,.2);

//Overlay:
$overlay-index:             $ui-main-nav-index - 10;
$overlay-color:             #121212;

//Cloud editable
$cloud-editable-padding:    10px;

$border-radius-base:        0px;
$border-radius-large:       0px;
$border-radius-small:       0px;

$font-size-base:            14px;
$font-size-large:           16px;
$font-size-small:           12px;
$font-size-extra-small:     10px;


$close-size:                16px;
$close-color:               $ui-txt-clr;

$font-size-cloud:           13px;

// Spacer
$cloud-spacer:              10px;

// Transition
$ui-transition:             .15s ease-in-out;

// Blur
$ui-blur:                   2px;

// UI dialog
$ui-dialog-border-top:      3px solid $cloud-brand;
$ui-dialog-spacer:          25px;
// Add Content block
$ui-add-block-btn-size:		80px;
$ui-add-block-icon-font:	30px;
$ui-add-block-icon-height:	44px;