// Copyed from old cloud.less

.cloud-list-item {
  border: 1px solid darken($border-clr, 10%);
  display: block;
  text-transform: uppercase;
  padding: 0.85em;
  margin:0 0.8*$spacer-x 0.8*$spacer-x 0;

  .btn {
    padding: 0px 0px;
    float: right;
    .icon {
      line-height: 16px;
      margin-top: -1px;
    }
  }

  span {
    vertical-align: middle;
  }
}

.cloud-page-status {
  color: $gray-lighter;
  font-size: 8px;
  position: absolute;
  top: 0;
  right: 0;
}

.more-page {
  margin-top: 15px;
  display: block;
  &:hover {
    text-decoration: none;
  }
}

.ui-sortable-placeholder {
  border: 1px solid $cloud-brand;
  visibility: visible!important;
}

.homebanner-settings {
  position: absolute;
  right: 10px;
  margin-top: 5px;
  color: $cloud-brand;
  z-index: $ui-main-nav-index;

  .icon {
    font-size: 25px;
  }
}

.cloud-highlight-content {
  @media(max-width: 1024px) {
    @include animation(cloudHighlight 2s);
  }
  @media(min-width: 1025px) {
    @include animation(cloudHighlight 1s);
  }
}

.cloud-thumbnail-loading {
  background-color: $gray-lighter-plus;
  height: 120px;
  padding: 1px;
  .c100 {
    float: none;
    margin: 38px auto 0 auto;
  }
}

.cloud-box-bg {
  background-color: $ui-main-nav-bg;
  padding: 11px;
}

.cloud-bo-liner {
  height: 40px;
  &:after {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    border-top: 1px solid $border-clr;
  }
}

.is-mobile {
  .cloud-ui-wrapper {
    .lang-management .group.default_lang {
      margin-bottom: $cloud-spacer;
    }

    .cloud-layer-panel-wrapper .btn-close-layout, .cloud-panel .cloud-panel-close {
      height: 50px;
      width: 50px;
      i {
        font-size: 16px;
      }
    }

    .cloud-panel .cloud-panel-close {
      &, &:active {
        background: transparent;
      }
    }
  }
}

.tooltip {
  z-index: $ui-main-nav-index + 2;
}

#privacy-policy-banner {
  &.missing-config {
    background-color: $cloud-brand;
    z-index: 9998;

    .cloud-close:hover {
      color: $cloud-brand;
    }
  }
}

.cookie-policy-wrapper {
  max-height: calc(100vh - 190px);
  overflow-y: auto;
}

.search-page-table-wrapper {
  height: calc(100vh - 200px);
  overflow: auto;

  .table {
    td, th {
      padding: 0.2rem 0.5rem;
      font-size: 0.75rem;
    }

    th {
      cursor: pointer;
      position: relative;
      padding-right: 24px;
      &:hover {
        cursor: pointer;
      }
      &:after {
        content: ' ';
        position: absolute;
        height: 0;
        width: 0;
        right: 10px;
        top: 16px;
        border-left: 5px solid transparent;
        border-right: 5px solid transparent;
        border-top: 5px solid #ccc;
        border-bottom: 0 solid transparent;
      }
      &.asc {
        &:hover {
          &:after {
            border-bottom: 5px solid #888;
          }
        }
        &:after {
          border-left: 5px solid transparent;
          border-right: 5px solid transparent;
          border-top: 0 solid transparent;
          border-bottom: 5px solid #333;
        }
      }
      &.desc {
        &:after {
          border-left: 5px solid transparent;
          border-right: 5px solid transparent;
          border-top: 5px solid #333;
          border-bottom: 5px solid transparent;
        }
      }
    }

    td {
      &.offline-line {
        border-left: 3px solid;
        border-left-color: $cloud-brand;
      }

      &.online-line {
        border-left: 3px solid;
        border-left-color: #218c31;
      }

      .is-drafted {
        color: $cloud-brand;
        margin-top: -.5em;
        font-size: 9px;
        &.icon-page-edit {
          font-size: $font-size-small;
        }
      }
    }

    tr {
      td:first-child:hover {
        background-color: lighten($gray-base, 80.0%);
        cursor: pointer;
      }
      &.sticky{
        position:sticky;
        top: 0 ;
      }
    }
  }
}

.brand-color {
  color: $cloud-brand;
}
