// Popin of add content

.add-content {
    .cloud-title {
        font-size: $font-size-large;
    }

    .cloud-section:not(:last-child) {
        padding-bottom: $cloud-spacer;
        margin-bottom: $cloud-spacer;
        border-bottom: 1px solid $border-clr-light;
    }

    .cloud-content-list {
        margin-bottom: 0;
        .btn {
            width: $ui-add-block-btn-size;
            height: $ui-add-block-btn-size;
            background-color: #fff;
            padding: 0 !important;
            &:hover {
                background-color: $gray-lighter-plus;
            }

            i.icon {
                font-size: $ui-add-block-icon-font;
                height: $ui-add-block-icon-height;
                line-height: $ui-add-block-icon-height;
                margin: 0;
            }
            .thumbnail {
                display: block;
                margin: 2px auto 6px auto;
                height: 32px!important;
                width: 100%!important;

                path {
                    fill: $ui-txt-clr;
                }
            }

            .cloud-label {
               color: $gray-lighter;
               font-size: $font-size-small;
               line-height: $font-size-small;
            }

            &.disabled {
                i.icon {
                    color: $gray-lighter;
                }
                &:hover {
                    background-color: transparent;
                }
            }
        }
    }
}

// Mobile
.is-mobile {
    .add-content {
        p.cloud-label {
            font-size: $font-size-cloud;
        }

        .list-inline-item {
            padding: 6px !important;
            margin: -2px;

            i.icon {
                font-size: $ui-add-block-icon-font;
            }

            span.cloud-label {
                font-size: $font-size-extra-small;
            }

            .btn {
                height: 100px;
            }
        }
    }
}
