// Details of every panel item

// Add new page
.new-page-wrapper {
  background-color: #fff;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: 90%;
  height: 150px;
  margin: auto;
  cursor: pointer;

  .is-mobile & {
    background-size: 80%;
  }

  p {
    text-align: center;
    padding-top: 10px;
    font-size: 11px;
    text-transform: uppercase;
  }

  &.blank {
    @include image-svg(new_page_blank);
  }

  &.article {
    @include image-svg(new_page_article);
  }

  &.custom_gallery {
    @include image-svg(new_page_gallery);
  }

  &.custom_contact {
    @include image-svg(new_page_contact);
  }

  &.custom_2_columns {
    @include image-svg(new_page_2_columns);
  }

  &.custom_3_columns {
    @include image-svg(new_page_3_columns);
  }

  &.custom_4_columns {
    @include image-svg(new_page_4_columns);
  }

  &.duplicate {
    @include image-svg(new_page_duplicate);
  }
}

.setting-link {
  color: $cloud-brand;
  display: block;
  font-size: 12px;
  margin-top: 5px;
  text-align: right;

  &:hover {
    text-decoration: underline;
  }
}

// Search Page
.search-page {
  .cloud-scroll {
    @include transition(all 300ms $ui-animation-function);
  }

  .filter-wrapper .cloud-input-group {
    padding-left: 14px;
    padding-right: 14px;
    @include transition(all 300ms $ui-animation-function);

    &.active {
      background: #dcdcdc;
      padding-top: 14px;
      padding-bottom: 24px;
    }
  }

  .filter-label {
    cursor: pointer;

    span:before {
      display: inline-block;
      width: 0;
      height: 0;
      vertical-align: .255em;
      content: "";
      border-top: .35em solid $cloud-brand;
      border-right: .3em solid transparent;
      border-bottom: 0;
      border-left: .3em solid transparent;
    }

    &.open {
      span:before {
        @include rotate(180deg);
      }
    }
  }

  span.clear-btn {
    float: right;
    cursor: pointer;
    color: $cloud-brand;
  }

  .dropdown {
    .dropdown-toggle {
      padding-top: 7px;
      padding-bottom: 7px;
      text-transform: none;
      margin-bottom: $cloud-spacer;
    }

    .dropdown-menu {
      background-color: #fff;
      width: 100%;
      text-align: left;
      border: 1px solid #ccc;
      border-top: 0;
      border-radius: 0;
      margin-top: 0;
      font-size: $font-size-cloud;
      padding: 0;

      .dropdown-item {
        padding: .25em 1em;
      }

      li {
        padding: 5px 15px;

        &:hover {
          background-color: #eaeaea;
        }
      }

      &.select-lang {
        li:not(#all) {
          text-transform: uppercase;
        }

        i.icon {
          margin-right: 5px;
        }
      }
    }
  }


  .filter-lang-wrapper .lang-option {
    text-transform: uppercase;
  }

  span.flag-icon {
    margin-right: 5px;
  }

  .filter-lang-wrapper, .filter-status-wrapper, .filter-create-date-wrapper {
    width: 100%;
  }

  .filter-btn-wrapper {
    display: table;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 15px;
    height: 26px;
    border: 1px solid $cloud-brand;
    border-radius: 2px;

    .active {
      background-color: $cloud-brand;
      color: #fff;
    }

    .btn {
      width: 85px;
      height: 24px;
      padding: 3px 1px;
      font-size: 12px;
      text-transform: capitalize;
    }
  }

  .cloud-search-results {
    li.cloud-resume {
      margin-bottom: $cloud-spacer;

      a.btn-page {
        border: 0;
        text-align: left;
        text-transform: none;
        padding: 6px;
        @include transition(none);

        &.is-online:before {
          content: "";
          background-color: #218c31;
          top: 0;
          bottom: 0;
          left: 0;
          position: absolute;
          width: 4px;
        }

        &.is-offline:before {
          content: "";
          background-color: #D94717;
          top: 0;
          bottom: 0;
          left: 0;
          position: absolute;
          width: 4px;
        }

        i.is-drafted {
          color: $cloud-brand;
          font-size: 9px;
          margin: 0;
          margin-top: -.5em;
          position: absolute;
          right: 10px;
          top: 50%;
        }

        .label {
          display: block;
          margin-left: 4px;
          margin-right: 35px;
          overflow: hidden;
          position: relative;

          &:after {
            content: "";
            position: absolute;
            top: 0;
            bottom: 0;
            right: 0;
            width: 25px;

            @include linear-gradient(trasparent, white);
          }

          &.has-lang {
            padding-left: 25px;

            .lang-label {
              color: $gray-lighter;
              font-size: 11px;
              left: 4px;
              position: absolute;
              text-transform: uppercase;
              top: 0;
            }
          }

        }

        &:hover .label:after {
          @include linear-gradient((red($gray-lighter-plus), green($gray-lighter-plus), blue($gray-lighter-plus), 0), $gray-lighter-plus);
        }
      }
    }
  }

  .infinity-loader {
    height: 30px;
    overflow: hidden;
  }
}

.publish-all-modified {
  background-color: $cloud-brand;
  color: #fff;

  .icon-play {
    color: #fff;
    margin-right: 7px;
    margin-bottom: 1px;
  }
}

&.is-mobile {
  .cloud-resume {
    .cloud-title {
      margin-bottom: 10px;

      a {
        padding: 12px 10px;

        i.is-drafted {
          right: 3px;
        }
      }
    }
  }
}

// Help link
.cloud-help-link {
  float: right;
  cursor: pointer;

  i {
    font-size: 18px;
  }
}

// User management
.user-management {

  li.user-item {
    .parameters {
      display: none;
    }

    span.toggle-user {
      position: absolute;
      top: 12px;
      right: 5px;
    }
  }

  li.user-item.open {
    span.name {
      font-weight: bold;
    }

    > .btn {
      &:hover {
        background-color: #fff;
        cursor: default;
      }
    }

    span.toggle-user {
      cursor: pointer;
    }

    .parameters {
      display: block;

      p.email {
        font-size: 9px;
      }

      .cloud-list {
        li {
          margin-bottom: 0px;

          button {
            padding: 10px;
            text-align: center;
            margin-bottom: 5px;

            &.delete  {
              color: $error-clr;
            }
          }
        }
      }
    }
  }
}

.pw-format-wrapper {
  width: 100%;
  background-color: #e0e0e0;
  padding: 10px 20px;
  z-index: 1;

  .icon-plus-circle {
    color: $error-clr;

    &:before {
      @include rotate(45deg);
      display: inline-block;
    }
  }

  .icon-check-circle {
    color: $success-clr;
  }
}

// Design Settings
.color-panel {
  .themes-link {
    color: $cloud-brand;
  }

  .input-group {
    padding: .5em;
    justify-content: space-between;
    border: 1px solid $border-clr;
    background: $control-bg;

    &.disabled {
      background: #e8e8e8;

      & * {
        cursor: no-drop;
      }
    }

    .input-group-append {
      width: 20px;
      justify-content: center;
      align-items: center;
      cursor: pointer;

      i {
        font-size: 1.4em;
      }
    }

    .color-overlay-undo {
      display: none;

      i {
        color: #fff;
        font-size: 18px;
      }
    }

    &.color-removed .color-overlay-undo {
      display: flex;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: rgba(#000, .3);
      text-align: center;
      justify-content: center;
      align-items: center;
    }
  }

  .label-color-panel {
    margin-bottom: 15px;
    border-bottom: 1px solid $gray-lighter;
  }

  .element_primary_color {
    .sp-replacer {
      margin-right: 1px;
    }

    .input-group-append {
      width: 0px;
    }
  }

  .element_custom_colors {

    .label-color-panel {
      margin-bottom: 15px;
      margin-top: 15px;
      border-bottom: 1px solid $gray-lighter;
    }

    .group.custom-colors {
      .color-add-new {
        .label-add-color {
          margin-right: 12px;
          margin-top: 2px;

          i {
            margin-right: 6px;
          }
        }
      }

      .add-color-text {
        position: absolute;
        top: 10px;
        left: 50%;
        transform: translateX(-50%);
      }

      .sp-replacer {
        margin-right: 1px;
      }

      .color-remove-btn {
        margin-left: 8px;
      }
    }
  }

  .color-panel-element {
    height: 25px;
  }
}

%color-hex {
  background: rgba($gray-base, .2);
  color: #fff;
  display: none;
  padding: 2px 10px;
  position: absolute;
  opacity: 0;
  text-transform: uppercase;
  @include transition(all $ui-transition);

  &:hover {
    background: rgba($gray-base, .6);
  }
}

// Theme panel
.theme-panel {
  background: #fff;
  padding: 1rem;

  form {
    margin-bottom: 0;

    & > [class*='element_']:not(:first-of-type) {
      margin-top: 1rem;
    }
  }

  .theme-color-panel {
    display: flex;
    padding: 2px;

    &.checked {
      box-shadow: 0px 0px 0px 2px $cloud-brand;
    }

    [class*='-color'] {
      height: 56px;
      flex-grow: 1;
      margin: 2px;
      border: 1px solid $border-clr;
      @include transition(all $ui-transition);

      span {
        @extend %color-hex;
      }

      &:hover {
        flex-grow: 2;

        span {
          display: block;
          opacity: 1;
        }
      }
    }
  }
}

//GlobalContent Panel
.global-content-settings-panel {
  .input-group {
    padding: .5em;
    justify-content: space-between;
    border: 1px solid $border-clr;
    background: $control-bg;
    align-items: center;

    &.dropdown-toggle:after {
      border-top: 0.35rem solid $cloud-brand;
      margin-left: 4px;
      margin-right: 6px;
    }

    .sp-preview {
      @include transition(all $ui-transition);

      span {
        @extend %color-hex;
        display: block;
        opacity: 1;
      }
    }
  }
}
