// Table

.cloud-table {
    font-size: 16px;
    width: 100%;
    max-width: 100%;
    margin-bottom: 3em;

    // Cells
    > thead,
    > tbody,
    > tfoot {
        > tr {
            > th,
            > td {
                padding: 10px 12px;
                vertical-align: top;
                text-align: left;
            }
        }
    }

    .hover {
        cursor: pointer;
    }

    &.cloud-table-bordered {
        // Cells
        > tbody,
        > tfoot {
            > tr {
                > th,
                > td {
                    border-bottom: 1px solid $border-clr;
                }
            }
        }
        > thead {
            > tr {
                > th,
                > td {
                    border-bottom: 2px solid $border-clr;
                }
            }
        }
    }

    &.cloud-table-hover {
        > tbody {
            tr:hover {
                background-color: lighten($gray-lighter-plus, 5%);
            }
        }
    }
}