// Editable elements

.cloud-edit {
    &:not(.rte) {
        -webkit-touch-callout: none !important;
        @include user-select(none);
    }

    .is-desktop &.add-padding {
        padding-left: $ui-main-nav-width;
        @include transition(all 150ms linear);
        .navbar {
            &, & .navbar-primary {
                padding-left: $ui-main-nav-width;
                @include transition(all 150ms linear);
            }
        }
    }
    .small-screen.is-desktop &.add-padding {
        padding-left: $ui-main-nav-width * 3/4;
        @include transition(all 150ms linear);
        .navbar {
            &, & .navbar-primary {
                padding-left: $ui-main-nav-width * 3/4;
                @include transition(all 150ms linear);
            }
        }
    }

    .is-desktop.small-screen & {
        padding-left: $ui-main-nav-width * 3/4;
    }
}

.move-mobile-content {
    text-align: center;
    padding: 40px;
    color: $cloud-brand;
    border: 1px dashed $cloud-brand;

    &.active {
        background-color: rgba(red($cloud-brand), green($cloud-brand), blue($cloud-brand), 0.1)
    }
}

.cloud-column-resize {
    position: absolute;
    top: 0;
    right: -10px;
    width: 20px;
    cursor: col-resize;
    z-index: 1001;

    &:hover {
        &:before {
            content: "";
            position: absolute;
            top: 0;
            bottom: 0;
            left: 50%;
            border-left: 2px solid;
            margin-left: -1px;
            border-color: $gray-lighter;
        }
    }
}

.cloud-dnd-content, .cloud-dnd {
    position: absolute;
    z-index: $dnd-index;

    &.dnd-hover {
        border-bottom: 4px solid $cloud-brand;
    }

    &.drop-hover {
        &.top {
            border-top: 4px solid $cloud-brand;
            left: 0 !important;
            right: 0 !important;
        }
        &.bottom {
            border-bottom: 4px solid $cloud-brand;
            left: 0 !important;
            right: 0 !important;
        }

        &.horizontal {
            &:after {
                content: "";
                position: absolute;
                right: 0;
                left: 0;
                top: 50%;
                border-bottom: 4px solid;
                margin-top: -2px;
                border-color: $cloud-brand;
            }

        }

        &.horizontal-between {
            + .horizontal.between {
                &:after {
                    content: "";
                    position: absolute;
                    right: 0;
                    left: 0;
                    top: 50%;
                    border-bottom: 4px solid;
                    margin-top: -2px;
                    border-color: $cloud-brand;
                }

                &.last {
                    &:after {
                        top: 0;
                    }
                }
            }
        }
    }
}

div.insert-content-left {
    &:after {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        border-left: 4px solid;
        margin-top: -2px;
        border-color: $cloud-brand;
    }
}

div.insert-content-right {
    border-right: 4px solid $cloud-brand;
}

.iframe-editor {
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
    z-index: 1;
}

.cloud-insert {
    cursor: pointer;
    min-height: $btn-plugin-w;
    position: relative;
    z-index: $insert-index;
    margin-top: -$btn-plugin-w / 2;

    .cloud-dnd:not(.between) &.first {
        margin-top: 0;
    }

    .btn-plugin {
        top: $btn-plugin-w / 2;
        z-index: $dnd-index + 1;
    }

    &:before {
        border-top: 4px solid $cloud-brand;
        content: "";
        left: 0;
        position: absolute;
        right: 0;
        top: $btn-plugin-w / 2 - 1;
        z-index: $dnd-index;
    }

    .cloud-dragging & {
        display: none;
    }

    &.fixed {
        padding-top: $btn-plugin-w / 2;
    }
}

.bb-content {
    &:hover {
        cursor: pointer;
    }

    &.cloud-highlight, &.cloud-highlight-move {
        position: relative;
        z-index: $ui-main-nav-index - 1;
        outline: 2px solid $cloud-brand;
        background-color: rgba($control-bg, .5);

        &:not([contenteditable='true']) {
            @include user-select(none);
        }
    }
}

.bb-dnd .rte-text {
    padding: 4px 0;
}