// Toolbar

// Toolbar main
.cloud-logo {
  display: block;
  height: $ui-main-nav-width / 2;
  background-color: #fff;
  @include image-svg(logo-backbee);
  background-size: cover;
  background-position-y: 50%;
}

&.is-desktop {
  & > .cloud-toolbar-main {
    &:before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      border-top: 2px solid $cloud-brand;
    }

    &.show {
      left: 0;
    }

    min-height: 34rem;
    background-color: $ui-main-nav-bg;
    position: fixed;
    z-index: $ui-main-nav-index;
    top: 0;
    left: -$ui-main-nav-width;
    bottom: 0;
    border-right: 1px solid $border-clr;
    @include user-select(none);

    @include transition(left 150ms linear);

    //nav
    #cloud-nav-submit {
      min-height: 40px;

      .btn-cancel {
        background-color: #fff;

        &:hover {
          background-color: $gray-lighter-plus;
        }

        a {
          padding: 10px 0;
          height: auto;

          i {
            display: none;
          }

          .cloud-label {
            font-size: 8px;
            font-weight: 700;
            line-height: normal;
            margin: 0;
            min-height: initial;
            padding: 0;
            opacity: 1;
          }
        }
      }
    }

    #cloud-nav-primary {
      background-color: $ui-main-nav-bg;
      position: absolute;
      top: 56%;
      @include translate3d(0, -50%, 0);
    }

    #cloud-nav-secondary {
      .btn {
        height: 3rem;
        padding: 0;
      }
      position: absolute;
      bottom: 0;
      left: 0;
    }

    .cloud-nav-wrapper {
      .online-switch {
        &.online {
          .cloud-label {
            border-left: 4px solid #218c31;
          }
        }

        &.offline {
          .cloud-label {
            border-left: 4px solid $cloud-brand;
          }
        }

        &.has-checkbox {
          .cloud-form-control {
            &:before {
              content: '';
              height: 24px;
              width: 4px;
              display: block;
              background-color: $cloud-brand;
              position: absolute;
              bottom: 0;
              left: 0;
            }
          }
        }
      }
    }

    .cloud-nav-wrapper > .nav {
      list-style: none;
      width: $ui-main-nav-width;
      > li {
        position: relative;
        > .btn {
          background: transparent;
          display: block;
          padding: 9px 0 17px 0;
          width: 100%;
          height: 70px;
          position: relative;
          z-index: $ui-main-nav-index+1;
          text-align: center;
          text-decoration: none;
          .icon {
            display: block;
            margin: 0;
            font-size: 26px;
            height: 35px;
            line-height: 35px;
            width: 100%;
            @include transition(200ms linear margin);

            &.icon-circle {
              background-color: $border-clr;
              margin: 0 auto;
              width: 50px;
              height: 50px;
              line-height: 50px;
              border-radius: 100%;
              margin-bottom: 5px;
            }
          }
          span {
            color: $gray-lighter;
            font-size: 8px;
            display: block;
            text-transform: uppercase;
            // padding: 0 5px;
            min-height: 18px;
            line-height: 1.2em;
            opacity: 0;
            @include transition(200ms linear all);
          }

          &:hover {
            background-color: $ui-panel-bg;

            span {
              opacity: 1;
            }
          }

          &.btn-publish {
            height: 78px;
            background-color: $btn-publish-bg;

            i {
              margin-top: 8px;
            }

            span {
              opacity: 1;
            }
          }

          &.btn-focus {
            background-color: $ui-panel-bg;
            border-top: 1px solid $border-clr;
            border-bottom: 1px solid $border-clr;

            span {
              opacity: 1;
            }

            &:after {
              content: "";
              border-left: 2px solid $ui-panel-bg;
              position: absolute;
              right: -2px;
              top: 0;
              bottom: 0;
            }
          }
        }
      }
    } //end nav

    .page-status {
      .cloud-label {
        font-size: 12px;
        line-height: 1.5;
      }
    }
  }
  .small-screen & > .cloud-toolbar-main {
    .cloud-nav-wrapper > .nav {
      list-style: none;
      width: $ui-main-nav-width  * 3/4;

      > li {
        > .btn {
          height: 60px;
          padding: 0;

          .icon {
            font-size: 22px;
            height: 30px;
            line-height: 30px;
          }

          &.btn-publish {
            height: 50px;
            padding-top: 4px;
            background-color: $btn-publish-bg;

            i {
              margin-top: 0;
            }
          }
        }
      }
    }

    .page-status {
      margin-top: 0 !important;
    }

    #cloud-nav-primary {
      top: 58%;
    }

    @media screen and ( max-height: 440px ){
      #cloud-nav-primary {
        top: unset;
        bottom: 0;
        transform: none;
      }

      #cloud-nav-secondary {
        display: none;
      }
    }
  }
}

// Mobile
&.is-mobile {
  position: fixed;
  z-index: $ui-main-nav-index;
  left: 0;
  right: 0;
  bottom: 0;

  & > .cloud-toolbar-main {
    .cloud-menu {
      position: absolute;
      bottom: 20px;
      left: 50%;
      z-index: $ui-main-nav-index;
      margin-left: -1.75em;
      padding-top: 0em;
      width: 84px;
      height: 84px;
      line-height: 84px;
      border: none;
      border-radius: 50%;
      background: none;
      background-color: $cloud-brand;
      text-align: center;
      font-weight: 700;
      font-size: 1.5em;
      text-transform: uppercase;
      cursor: pointer;
      -webkit-backface-visibility: hidden;
      outline: 0;

      .icon {
        font-size: 1.6em;
        line-height: 84px;
        color: #fff;
        margin: 0;
      }

      &:active {
        background-color: darken($cloud-brand,5%);
      }
    }

    .cloud-nav-wrapper {
      border-top: 2px solid $cloud-brand;
      background-color: $ui-main-nav-bg;
      position: absolute;
      bottom: 0;
      width: 100%;
      z-index: $ui-main-nav-index + 1;

      @include transition(all 300ms $ui-animation-function);
      @include translate3d(0, 100%, 0);

      -webkit-touch-callout: none !important;

      .menu-handler {
        padding: 2px 0;
        text-align: center;
        line-height: 5px;

        span {
          display: none;
        }
      }

      > .nav {
        margin: 5px 8px;

        &#cloud-nav-primary li {
          display: block;
          float: left;
          width: 50%;
          padding: 5px 3px;
          box-sizing: border-box;
          a.btn {
            width: 100%;
            padding: 10px;
            i.icon, .cloud-label {
              display: inline-block;
            }
          }

          &.btn-cancel {
            float: right;
            a.btn {
              background: $border-clr;
              border-color: $editor-clr;
            }
          }
        }

        &#cloud-nav-secondary li {
          position: relative;
          display: block;
          float: left;
          width: 33.333%;
          padding: 5px 3px;
          box-sizing: border-box;
          > .btn {
            background-color: #fff;
            display: block;
            text-align: center;
            text-decoration: none;
            text-transform: uppercase;
            height: 80px;
            width: 100%;
            padding: 18px 0 0 0;
            @include box-shadow(none);
            outline-width: 0;

            @include transition(background 500ms $ui-animation-function);

            .icon {
              font-size: 22px;
              line-height: 28px;
              width: auto;
              margin: 0 0 10px 0;
            }
            .cloud-label {
              display: block;
              font-size: 8px;
              line-height: 1.1em;
            }

            &:active {
              background-color: darken(#fff, 20%);
              @include transition-duration(0.1s);
            }

            &.btn-publish {
              background-color: $btn-publish-bg;
              color: $btn-publish-clr;
              position: relative;
              span {
                color: $btn-publish-clr!important;
              }

              i {
                color: #fff;
              }
            }
          }
        }
      }

      &.cloud-menu-plugin {

        border-width: 0;
        padding: 20px 15px;

        .menu-handler {
          display: none;
        }

        .btn-close-menu {
          display: block;
          margin-top: 15px;
          width: 100%;
        }

        #cloud-nav-primary {

          background-color: $ui-main-nav-bg;
          padding: 15px;
          margin: 0;

          > li {
            float: none;
            width: 100%;
            padding: 0;

            + li {
              margin-top: 10px;
            }

            > .btn {

              text-align: left;

              text-transform: uppercase;
              height: auto;
              width: 100%;
              padding: 14px 15px;
              border: 1px solid darken($ui-main-nav-bg, 5%);

              .icon {
                color: $cloud-brand;
                font-size: 19px;
                width: auto;
                margin: 0 5px 0 0;
              }
              .cloud-label {
                display: inline;
                font-size: 13px;
              }

              &:active {
                background-color: darken(#fff, 20%);
                @include transition-duration(0.1s);
              }
            }

          }
        }
      }
    }
  }

  //Open
  &.opened-nav {
    // Btn
    .cloud-menu {
      display: none;
    }

    .cloud-nav-wrapper {
      @include translate3d(0, 0, 0);
    }
  }
}

.cloud-liner:after {
  border-top: 1px solid $border-clr;
}