.cloud-nav {
	$font-size: 14px;
	$border-radius: 4px;

	display: block;
	margin-bottom: 3em;

	.cloud-pagination,
	.cloud-pager {
		clear: both;
		&:before,
		&:after {
			content: "";
			clear: both;
			display: table;
		}

		> li {
			display: inline;
			font-size: $font-size;
			> a,
			> span {
				position: relative;
				height: 36px;
				min-width: 36px;
				float: left; // Collapse white-space
				font-size: $font-size;
				padding: 4px;
				line-height: 28px;
				text-decoration: none;
				background-color: #fff;
				border: 0;
				margin-left: -1px;
				text-align: center;
			}
			&:first-child {
				> a,
				> span {
					margin-left: 0;
					border-radius: $border-radius 0 0 $border-radius;
				}
			}
			&:last-child {
				> a,
				> span {
					border-radius: 0 $border-radius $border-radius 0;
				}
			}
		}

		> li > a,
		> li > span {
			&:hover,
			&:focus {
				z-index: 2;
				color: #fff;
				background-color: $cloud-brand;
			}
		}

		> .active > a,
		> .active > span {
			&,
			&:hover,
			&:focus {
				z-index: 3;
				background-color: #fff;
				color: $cloud-brand;
				font-weight: 700;
				cursor: default;
			}
		}

		> .disabled {
			> span,
			> span:hover,
			> span:focus,
			> a,
			> a:hover,
			> a:focus {
				color: $ui-txt-clr;
				background-color: $gray-lighter-plus;
				border-color: $gray-lighter-plus;
				cursor: default;
			}
		}
	}

	.cloud-pager {
		> li {
			> a,
			> span {
				border-radius: $border-radius!important;
				padding-left: 1em;
				padding-right: 1em;
			}
			+ li {
				a {
					margin-left: 1em;
				}
			}
		}
	}
}
