// Cloud overlay

.cloud-overlay, #cloud-dropback, {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba($overlay-color, 0.5);
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    opacity: 1;
    z-index: $overlay-index + 1;

    &.d-none {
        opacity: 0;
        z-index: 0;
    }

    &:not(.no-background):not(.reduce-opacity) {
        @include animation(overlayFadeIn 400ms);
    }

    &.no-background {
        background-color: transparent;
    }

    &.reduce_opacity {
        background-color: rgba($overlay-color, 0.09);
    }

    &.panel {
        z-index: $ui-panel-index - 1;
    }
}

.cloud-overlay-menu {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: $ui-main-nav-index + 1;
}

.cloud-no-scroll {
    &, body {
        height: 100%;
        overflow-y: hidden;
        -webkit-overflow-scrolling: none;
    }
}
