// Form

.cloud-control {
  background-color: $control-bg;
  border: 1px solid $border-clr;
  color: $gray-darker;
  padding: 18px;
  font-family: $font-family-default;
  font-size: $font-size-cloud;
  border-radius: 0;
  @include placeholder($gray-lighter, italic);

  &.cloud-select {
    position: relative;
    padding: 0;
    width: auto;
    vertical-align: middle;

    select {
      color: $gray-darker;
      background: $control-bg;
      cursor: pointer;
      border: 0;
      padding: 14px 42px 14px 15px;
      outline: 0;
      width: 100%;
      @include appearance-none;
    }
    /* Dropdown arrow */
    &:after {
      position: absolute;
      top: 50%;
      right: 1.25rem;
      content: "";
      width: 0;
      height: 0;
      margin-top: -.15rem;
      pointer-events: none;
      border-top: .35rem solid $cloud-brand;
      border-right: .35rem solid transparent;
      border-bottom: .35rem solid transparent;
      border-left: .35rem solid transparent;
    }
  }

}

textarea.cloud-control {
  width: 100%;
}

form, .form-group-content {
  font-size: $font-size-cloud;
  margin-bottom: $spacer * 2;

  .nav-tabs {
    border-bottom: 0;
    margin-bottom: $cloud-spacer;
    display: table;
    width: 100%;
    .nav-item {
      display: table-cell;
      text-align: center;
      border-radius: 0;
      margin-bottom: -1px;
      border: 1px solid transparent;
      vertical-align: middle;

      .nav-link{
        &:focus {
          text-decoration: none;
          outline: 0;
        }

        &, &:hover {
          border-color: transparent;
        }
      }
      &:not(.active) {
        border-bottom: 1px solid $border-clr;
      }
      &.active {
        cursor: default;
        border: 1px solid $border-clr;
        border-bottom-color: transparent;

        .nav-link {
          color: $cloud-brand;
        }
      }
    }
  }

  .group {
    &:not(:first-of-type) {
      margin-top: $cloud-spacer;
      padding-top: $cloud-spacer;
      border-top: 1px solid #ddd;
    }

    .group-label {
      padding: 7px 0;
      font-weight: bold;
    }

    .group-description {
      margin-bottom: $cloud-spacer;
    }
  }

  .group-submit {
    margin-top: $cloud-spacer;
    padding-top: $cloud-spacer;
    border-top: 1px solid #ddd;
  }
}

button.input-group-text {
  background: #fff;
  border-color: $border-clr;
  border-left: none;
}

.cloud-menu-parameters {
