// Switch
.cloud-switch {
  $switch-height : 24px;
  .cloud-form-control {
    padding-left: 0;
    display: block;
    line-height: $switch-height;
    margin-top: .5em;
    margin-bottom: 0;

    .cloud-switch-indicator {
      display: block;
      float: right;
      padding: 2px;
      width: 40px;
      height: $switch-height;
      background-color: $border-clr;
      border-radius: 30px;
      position: relative;

      &:before,
      &:after {
        display: block;
        position: absolute;
        top: 1px;
        left: 1px;
        bottom: 1px;
        content: "";
      }


      &:before {
        right: 1px;
        background-color: #f1f1f1;
        border-radius: 30px;
        @include transition(background 0.4s);
      }
      &:after {
        width: 22px;
        background-color: #fff;
        border-radius: 100%;
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
        @include transition(margin 0.4s);
      }
    }



    input:checked + .cloud-switch-indicator:before {
      background-color: $cloud-brand;
    }
    input:checked + .cloud-switch-indicator:after {
      margin-left: 16px;
    }

    input:disabled + .cloud-switch-indicator {
      cursor: default;
      opacity: 0.6;
    }
  }

  & + .cloud-input {
    margin-top: 5px;
  }
}

.cloud-form-inline {
  .cloud-label, .cloud-control {
    display: inline-block;
    width: auto;
    margin-left: 5px;
    margin-right: 5px;
  }
}

