    font-size: 1.2em;
    height: 50px;
    line-height: 50px;
    width: 50px;
    text-align: center;
  }
  .cloud-label {
    display: block;
    font-size: 11px;
  }
}

.cloud-btn-group {
  .btn {
    margin-right: 10px;
  }
}

