//Divers
.cloud-divider {
  border-top: 1px solid mix($ui-panel-bg, $border-clr, 20%);
  margin-bottom: $spacer-y;
  margin-top: $spacer-y;
}

.cloud-form-control {
  position: relative;
  display: inline-block;
  padding-left: 32px;
  cursor: pointer;

  &.disabled {
    color: $gray-lighter;
    cursor: default;
  }

  input {
    position: absolute;
    opacity: 0;
    z-index: -1;
    /* Put the input behind the label so it doesn't overlay text */
  }
  .cloud-label {
    font-size: 13px;
  }
  .cloud-control-indicator {
    position: absolute;
    left: 0;
    width: 22px;
    height: 22px;
    line-height: 22px;
    font-size: 65%;
    color: #eee;
    text-align: center;
    background-color: $control-bg;
    background-size: 50% 50%;
    background-position: center center;
    background-repeat: no-repeat;
    border: 1px solid $border-clr;
    @include user-select(none);
  }
  //radio
  &.cloud-radio {
    .cloud-control-indicator {
      border-radius: 50%;
    }
    input:checked ~ .cloud-control-indicator {
      &:after {
        content: "";
        border-radius: 100%;
        display: inline-block;
        width: 8px;
        height: 8px;
        background-color: $cloud-brand;
      }
    }
    &:not(:last-child) {
      margin-right: 1rem;
    }
  }
  //checkbox
  &.cloud-checkbox {
    padding: $cloud-spacer $cloud-spacer 0 $cloud-spacer * 3;
    input:checked ~ .cloud-control-indicator {
      text-align: center;
      &:after {
        content: $cloud-var-check;
        color: $cloud-brand;
        font-family: 'font-cloud';
        font-size: 16px;
        display: inline-block;
      }
    }

    input:disabled ~  .cloud-control-indicator {
      background-color: #ddd;
    }
  }

  &.cloud-form-sm {
    .cloud-control-indicator {
      top: 6px;
      width: 17px;
      height: 17px;
      line-height: 17px;
    }
  }
}

.cloud-input-group {
  display: table;
  width: 100%;
  .cloud-control {
    display: table-cell;
    width: 100%;
    height: 50px;
    float: left;

    &:first-child {
      border-right: 0;
    }

    &.search-box {
      border-right: 0;
    }
  }
  .cloud-input-group-btn {
    display: table-cell;
    width: 1%;
    vertical-align: top;
  }
  .input-group-addon {
    background-color: $control-bg;
    border-radius: 0;
    padding:14px;
    color: $cloud-brand;

    &.page-search {
      padding: 0px;
      border-color: #ddd;

      .cloud-select {
        display: inline-block;
        border-left: 1px solid $border-clr;
        border-right: 1px solid $border-clr;
        width: 47px;

        select {
          padding-left: 2px;
          padding-right: 15px;
          text-transform: uppercase;
          font-size: 8px;
        }

        &:after {
          right: 3px;
          border-top: .35rem solid #777;
        }
      }

      .btn-submit {
        padding: 12px;
        //border-left: 1px solid #ddd;
        .icon {
          margin: 0;
          color: $cloud-brand;
        }
      }
    }

    + .cloud-control {
      border-left-width: 0;
    }
  }

  .cloud-control {
    border-radius: 0;
    box-shadow: none;
  }
}

.cloud-choice {
  &.cloud-box-bg {
    padding-top: 20px;
    padding-left: 85px;
  }
  label {
    margin: 0!important;
    padding: 0;
  }
  margin-bottom: 20px;
  position: relative;
  .cloud-form-control {
    border-right: 1px solid darken($ui-main-nav-bg, 10%);
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    padding: 0;
    width: 60px;
    .cloud-control-indicator {
      position: absolute;
      left: 50%;
      top: 50%;
      margin: -11px 0 0 -11px;
    }
  }
}

.cloud-dropzone {
  position: relative;
  min-height: 130px;
  border: none;
  cursor: pointer;

  .cloud-placeholder {
    margin: 5px;
    border: 1px solid $border-clr;

    .cloud-placeholder-content {
      .ico {
        width: 42px;
        height: 42px;
        line-height: 40px;
        margin-bottom: 5px;
        i {
          font-size: 22px;
        }
      }
    }
  }
}

.ui-dialog-content {
  .popin-content, .dropzone {
    height: 100%;
  }
  .cloud-dropzone.cloud-dropzone-outline .cloud-placeholder {
    background: transparent;
    border: 2px dashed #bbb;
    .ico {
      width: 52px;
      height: 52px;
      background: transparent;
      i {
        font-size: 46px;
      }
    }
    &:after {
      display: none;
    }
  }
}

