// Reset Cloud

* {
    color: $ui-txt-clr;
    font-family: $font-family-default;
}

a {
    color: $ui-txt-clr;
}

.btn, button {
    border-radius: 0;
    color: $ui-txt-clr;

    &:focus, &:active {
        outline: none;
        box-shadow: none;
    }

    i {
        color: $gray;
    }

    &.btn-primary {
        &:active {
            background-color: $cloud-brand;
            border-color: $cloud-brand;
            &:focus {
                box-shadow: none;
            }
        }
    }
}

.form-control, .input-group-text {
    color: $ui-txt-clr;
    border-color: $border-clr;
    border-radius: 0;
    &:focus {
        box-shadow: none;
    }
}

.underline {
    text-decoration: underline;
}

.clearfix {
    &:before,
    &:after {
        content: "";
        display: table;
    }
    &:after {
        clear: both;
    }
}

.dropdown {
    width: 100%;
    .dropdown-toggle {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        &:hover {
            background: #fff;
        }
        &:after {
            border-top: 0.35rem solid $cloud-brand;
        }
    }
    .dropdown-menu {
        .dropdown-item {
            cursor: pointer;
            &:active, &.active {
                background: transparent;
                &:hover {

                }
            }
        }
    }
}

.blur {
    @include blur($ui-blur);
}

.cloud-relative {
    position: relative !important;
}

//reset theme
.navbar-fixed-top.affix {
    opacity: 0;
    z-index: -1;
}

//cursor: pointer
.cursor-pointer {
    cursor: pointer;
}
