.cloud-row {
    margin-left: -$spacer-x/2;
    margin-right: -$spacer-x/2;
    margin-bottom: $cloud-spacer * 2;

    @extend .clearfix;

    .cloud-col-2,
    .cloud-col-3,
    .cloud-col-4,
    .cloud-col-5,
    .cloud-col-6,
    .cloud-col-7,
    .cloud-col-8,
    .cloud-col-10,
    .cloud-col-12 {
    min-height: 1px;
    float: left;
    padding-left: $spacer-x/2;
    padding-right: $spacer-x/2;
    @include box-sizing(border-box);
    }

    .cloud-col-2 {
    width: 16.6667%;
    }

    .cloud-col-3 {
    width: 25%;
    }

    .cloud-col-4 {
    width: 33.33333%;
    }

    .cloud-col-5 {
    width: 41.6667%;
    }

    .cloud-col-6 {
    width: 50%;
    }

    .cloud-col-7 {
    width: 58.33333%;
    }

    .cloud-col-8 {
    width: 66.66666%;
    }

    .cloud-col-10 {

    width: 83.3333%;
    }

    .cloud-col-12 {
    width: 100%;
    }
}

// Front

// Spacer for add content
.rootcontentset {
    & > .bb-content {
        margin-bottom: 1em;
    }
}