.cke_reset {
  margin: 0;
  padding: 0;
  border: 0;
  background: transparent;
  text-decoration: none;
  width: auto;
  height: auto;
  vertical-align: baseline;
  box-sizing: content-box;
  position: static;
  transition: none
}

.cke_reset_all,
.cke_reset_all *,
.cke_reset_all a,
.cke_reset_all textarea {
  margin: 0;
  padding: 0;
  border: 0;
  background: transparent;
  text-decoration: none;
  vertical-align: baseline;
  box-sizing: content-box;
  transition: none;
  border-collapse: collapse;
  font: normal normal normal 12px Arial, Helvetica, Tahoma, Verdana, Sans-Serif;
  color: #000;
  text-align: left;
  white-space: nowrap;
  cursor: auto;
  float: none
}

.cke_reset_all .cke_rtl * {
  text-align: right
}

.cke_reset_all iframe {
  vertical-align: inherit
}

.cke_reset_all textarea {
  white-space: pre-wrap
}

.cke_reset_all textarea,
.cke_reset_all input[type="text"],
.cke_reset_all input[type="password"] {
  cursor: text
}

.cke_reset_all textarea[disabled],
.cke_reset_all input[type="text"][disabled],
.cke_reset_all input[type="password"][disabled] {
  cursor: default
}

.cke_reset_all fieldset {
  padding: 10px;
  border: 2px groove #e0dfe3
}

.cke_reset_all select {
  box-sizing: border-box
}

.cke_reset_all table {
  table-layout: auto
}

.cke_chrome {
  display: block;
  border: 1px solid #d1d1d1;
  padding: 0
}

.cke_inner {
  display: block;
  background: #fff;
  padding: 0;
  -webkit-touch-callout: none
}

.cke_float {
  border: 0
}

.cke_float .cke_inner {
  padding-bottom: 0
}

.cke_top,
.cke_contents,
.cke_bottom {
  display: block;
  overflow: hidden
}

.cke_top {
  border-bottom: 1px solid #d1d1d1;
  background: #f8f8f8;
  padding: 6px 8px 2px;
  white-space: normal
}

.cke_float .cke_top {
  border: 1px solid #d1d1d1
}

.cke_bottom {
  padding: 6px 8px 2px;
  position: relative;
  border-top: 1px solid #d1d1d1;
  background: #f8f8f8
}

.cke_browser_ios .cke_contents {
  overflow-y: auto;
  -webkit-overflow-scrolling: touch
}

.cke_resizer {
  width: 0;
  height: 0;
  overflow: hidden;
  border-width: 10px 10px 0 0;
  border-color: transparent #bcbcbc transparent transparent;
  border-style: dashed solid dashed dashed;
  font-size: 0;
  vertical-align: bottom;
  margin-top: 6px;
  margin-bottom: 2px
}

.cke_hc .cke_resizer {
  font-size: 15px;
  width: auto;
  height: auto;
  border-width: 0
}

.cke_resizer_ltr {
  cursor: se-resize;
  float: right;
  margin-right: -4px
}

.cke_resizer_rtl {
  border-width: 10px 0 0 10px;
  border-color: transparent transparent transparent #bcbcbc;
  border-style: dashed dashed dashed solid;
  cursor: sw-resize;
  float: left;
  margin-left: -4px;
  right: auto
}

.cke_wysiwyg_div {
  display: block;
  height: 100%;
  overflow: auto;
  padding: 0 8px;
  outline-style: none;
  box-sizing: border-box
}

.cke_panel {
  visibility: visible;
  width: 120px;
  height: 100px;
  overflow: hidden;
  background-color: #fff;
  border: 1px solid #d1d1d1
}

.cke_menu_panel {
  padding: 0;
  margin: 0
}

.cke_combopanel {
  width: 150px;
  height: 170px
}

.cke_panel_frame {
  width: 100%;
  height: 100%;
  font-size: 12px;
  overflow: auto;
  overflow-x: hidden;

  .cke_panel_container {
    color: red;
  }
}

.cke_panel_container {
  overflow-y: auto;
  overflow-x: hidden
}

.cke_panel_block:focus {
  outline: 0
}

.cke_panel_list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  white-space: nowrap
}

.cke_panel_listItem {
  margin: 0;
  padding: 0
}

.cke_panel_listItem a {
  padding: 6px 7px;
  display: block;
  color: inherit !important;
  text-decoration: none;
  overflow: hidden;
  text-overflow: ellipsis
}

.cke_hc .cke_panel_listItem a {
  border-style: none
}

.cke_panel_listItem.cke_selected a,
.cke_panel_listItem a:hover,
.cke_panel_listItem a:focus,
.cke_panel_listItem a:active {
  background-color: #e9e9e9
}

.cke_panel_listItem a:focus {
  outline: 1px dotted #000
}

.cke_hc .cke_panel_listItem a:hover,
.cke_hc .cke_panel_listItem a:focus,
.cke_hc .cke_panel_listItem a:active {
  border: 2px solid;
  padding: 4px 5px
}

.cke_panel_listItem p,
.cke_panel_listItem h1,
.cke_panel_listItem h2,
.cke_panel_listItem h3,
.cke_panel_listItem h4,
.cke_panel_listItem h5,
.cke_panel_listItem h6,
.cke_panel_listItem pre {
  margin-top: 0;
  margin-bottom: 0
}

.cke_panel_grouptitle {
  cursor: default;
  font-size: 11px;
  font-weight: bold;
  white-space: nowrap;
  margin: 0;
  padding: 6px 6px 7px 6px;
  color: #484848;
  border-bottom: 1px solid #d1d1d1;
  background: #f8f8f8
}

.cke_colorblock {
  padding: 10px;
  font-size: 11px;
  font-family: 'Microsoft Sans Serif', Tahoma, Arial, Verdana, Sans-Serif
}

.cke_colorblock,
.cke_colorblock a {
  text-decoration: none;
  color: #000
}

a.cke_colorbox {
  padding: 2px;
  float: left;
  width: 20px;
  height: 20px
}

.cke_rtl a.cke_colorbox {
  float: right
}

a:hover.cke_colorbox,
a:focus.cke_colorbox,
a:active.cke_colorbox {
  outline: 0;
  padding: 0;
  border: 2px solid #139ff7
}

a:hover.cke_colorbox {
  border-color: #bcbcbc
}

span.cke_colorbox {
  width: 20px;
  height: 20px;
  float: left
}

.cke_rtl span.cke_colorbox {
  float: right
}

a.cke_colorauto,
a.cke_colormore {
  border: #fff 1px solid;
  padding: 3px;
  display: block;
  cursor: pointer
}

a.cke_colorauto {
  padding: 0;
  border: 1px solid transparent;
  margin-bottom: 6px;
  height: 26px;
  line-height: 26px
}

a.cke_colormore {
  margin-top: 10px;
  height: 20px;
  line-height: 19px
}

a:hover.cke_colorauto,
a:hover.cke_colormore,
a:focus.cke_colorauto,
a:focus.cke_colormore,
a:active.cke_colorauto,
a:active.cke_colormore {
  outline: 0;
  border: #139ff7 1px solid;
  background-color: #f8f8f8
}

a:hover.cke_colorauto,
a:hover.cke_colormore {
  border-color: #bcbcbc
}

.cke_colorauto span.cke_colorbox {
  width: 18px;
  height: 18px;
  border: 1px solid #808080;
  margin-left: 1px;
  margin-top: 3px
}

.cke_rtl .cke_colorauto span.cke_colorbox {
  margin-left: 0;
  margin-right: 1px
}

span.cke_colorbox[style*="#ffffff"],
span.cke_colorbox[style*="#FFFFFF"],
span.cke_colorbox[style="background-color:#fff"],
span.cke_colorbox[style="background-color:#FFF"],
span.cke_colorbox[style*="rgb(255,255,255)"],
span.cke_colorbox[style*="rgb(255, 255, 255)"] {
  border: 1px solid #808080;
  width: 18px;
  height: 18px
}

.cke_toolbar {
  float: left
}

.cke_rtl .cke_toolbar {
  float: right
}

.cke_toolgroup {
  border: 0;
  float: left;
  margin: 1px 0 6px 0;
}

.cke_rtl .cke_toolgroup {
  float: right;
  margin: 1px 0 6px 2px;
  padding-left: 3px;
  padding-right: 0
}

.cke_hc .cke_toolgroup {
  margin-right: 5px;
  margin-bottom: 5px
}

.cke_hc.cke_rtl .cke_toolgroup {
  margin-right: 0;
  margin-left: 5px
}

a.cke_button {
  display: inline-block;
  height: 18px;
  padding: 4px 6px;
  outline: 0;
  cursor: default;
  // float: left;
  border: 0;
  position: relative;

  #cloud-ui & {
    @extend a.cke_button;
  }
}

.cke_rtl a.cke_button {
  float: right
}

.cke_hc a.cke_button {
  border: 1px solid black;
  padding: 3px 5px;
  margin: 0 3px 5px 0
}

.cke_hc.cke_rtl a.cke_button {
  margin: 0 0 5px 3px
}

a.cke_button_on {
  background: #fff;
  border: 1px #bcbcbc solid;
  padding: 3px 5px
}

a.cke_button_off:hover,
a.cke_button_off:focus,
a.cke_button_off:active {
  background: #e5e5e5;
  border: 1px #bcbcbc solid;
  padding: 3px 5px;

  #cloud-ui & {
    @extend a.cke_button_off:hover;
  }
}

.cke_hc a.cke_button_on,
.cke_hc a.cke_button_off:hover,
.cke_hc a.cke_button_off:focus,
.cke_hc a.cke_button_off:active {
  background: #e5e5e5;
  border: 3px solid #000;
  padding: 1px 3px;
}

a.cke_button_disabled:hover,
a.cke_button_disabled:focus,
a.cke_button_disabled:active {
  border: 0;
  padding: 4px 6px;
  background-color: transparent;
}

a.cke_button_disabled:focus {
  border: 1px #bcbcbc solid;
  padding: 3px 5px;
}

.cke_hc a.cke_button_disabled:hover,
.cke_hc a.cke_button_disabled:focus,
.cke_hc a.cke_button_disabled:active {
  border: 1px solid #acacac;
  padding: 3px 5px;
  margin: 0 3px 5px 0
}

.cke_hc a.cke_button_disabled:focus {
  border: 3px solid #000;
  padding: 1px 3px
}

.cke_hc.cke_rtl a.cke_button_disabled:hover,
.cke_hc.cke_rtl a.cke_button_disabled:focus,
.cke_hc.cke_rtl a.cke_button_disabled:active {
  margin: 0 0 5px 3px
}

a.cke_button_disabled .cke_button_icon,
a.cke_button_disabled .cke_button_arrow {
  opacity: .3
}

.cke_hc a.cke_button_disabled {
  border-color: #acacac
}

.cke_hc a.cke_button_disabled .cke_button_icon,
.cke_hc a.cke_button_disabled .cke_button_label {
  opacity: .5
}

.cke_toolgroup a.cke_button:last-child:after,
.cke_toolgroup a.cke_button.cke_button_disabled:hover:last-child:after {
  content: "";
  position: absolute;
  height: 18px;
  width: 0;
  border-right: 1px solid #bcbcbc;
  margin-top: 4px;
  top: 0;
  right: -3px
}

.cke_rtl .cke_toolgroup a.cke_button:last-child:after,
.cke_rtl .cke_toolgroup a.cke_button.cke_button_disabled:hover:last-child:after {
  border-right: 0;
  right: auto;
  border-left: 1px solid #bcbcbc;
  top: 0;
  left: -3px
}

.cke_hc .cke_toolgroup a.cke_button:last-child:after,
.cke_hc .cke_toolgroup a.cke_button.cke_button_disabled:last-child:after,
.cke_hc .cke_toolgroup a.cke_button.cke_button_disabled:hover:last-child:after {
  border-color: #000;
  top: 0;
  right: -7px
}

.cke_hc.cke_rtl .cke_toolgroup a.cke_button:last-child:after,
.cke_hc.cke_rtl .cke_toolgroup a.cke_button.cke_button_disabled:last-child:after,
.cke_hc.cke_rtl .cke_toolgroup a.cke_button.cke_button_disabled:hover:last-child:after {
  top: 0;
  right: auto;
  left: -7px
}

.cke_toolgroup a.cke_button:hover:last-child:after,
.cke_toolgroup a.cke_button.cke_button_on:last-child:after {
  top: -1px;
  right: -4px
}

.cke_rtl .cke_toolgroup a.cke_button:hover:last-child:after,
.cke_rtl .cke_toolgroup a.cke_button.cke_button_on:last-child:after {
  top: -1px;
  right: auto;
  left: -4px
}

.cke_hc .cke_toolgroup a.cke_button:hover:last-child:after,
.cke_hc .cke_toolgroup a.cke_button.cke_button_on:last-child:after {
  top: -2px;
  right: -9px
}

.cke_hc.cke_rtl .cke_toolgroup a.cke_button:hover:last-child:after,
.cke_hc.cke_rtl .cke_toolgroup a.cke_button.cke_button_on:last-child:after {
  top: -2px;
  right: auto;
  left: -9px
}

.cke_toolbar.cke_toolbar_last .cke_toolgroup a.cke_button:last-child:after {
  content: none;
  border: 0;
  width: 0;
  height: 0
}

.cke_button_icon {
  cursor: inherit;
  background-repeat: no-repeat;
  margin-top: 1px;
  width: 20px;
  height: 20px;
  font-size: 18px;
  // float: left;
  display: inline-block;

  @extend .icon;
}

.cke_rtl .cke_button_icon {
  float: right
}

.cke_hc .cke_button_icon {
  display: none
}

.cke_button_label {
  display: none;
  padding-left: 3px;
  margin-top: 1px;
  line-height: 17px;
  vertical-align: middle;
  float: left;
  cursor: default;
  color: #484848
}

.cke_rtl .cke_button_label {
  padding-right: 3px;
  padding-left: 0;
  float: right
}

.cke_hc .cke_button_label {
  padding: 0;
  display: inline-block;
  font-size: 12px
}

.cke_button_arrow {
  display: inline-block;
  margin: 8px 0 0 1px;
  width: 0;
  height: 0;
  cursor: default;
  vertical-align: top;
  border-left: 3px solid transparent;
  border-right: 3px solid transparent;
  border-top: 3px solid #484848
}

.cke_rtl .cke_button_arrow {
  margin-right: 5px;
  margin-left: 0
}

.cke_hc .cke_button_arrow {
  font-size: 10px;
  margin: 3px 0 0 3px;
  width: auto;
  border: 0
}

.cke_rtl .cke_toolbar_separator {
  float: right
}

.cke_hc .cke_toolbar_separator {
  background-color: #000;
  margin-left: 2px;
  margin-right: 5px;
  margin-bottom: 9px
}

.cke_hc.cke_rtl .cke_toolbar_separator {
  margin-left: 5px;
  margin-right: 2px
}

.cke_toolbar_break {
  display: block;
  clear: left
}

.cke_rtl .cke_toolbar_break {
  clear: right
}

a.cke_toolbox_collapser {
  width: 12px;
  height: 11px;
  float: right;
  margin: 11px 0 0;
  font-size: 0;
  cursor: default;
  text-align: center;
  border: 1px solid #bcbcbc
}

.cke_rtl .cke_toolbox_collapser {
  float: left
}

.cke_toolbox_collapser:hover {
  background: #e5e5e5
}

.cke_toolbox_collapser.cke_toolbox_collapser_min {
  margin: 0 2px 4px
}

.cke_toolbox_collapser .cke_arrow {
  display: inline-block;
  height: 0;
  width: 0;
  font-size: 0;
  margin-top: 1px;
  border: 3px solid transparent;
  border-bottom-color: #484848
}

.cke_toolbox_collapser.cke_toolbox_collapser_min .cke_arrow {
  margin-top: 4px;
  border-bottom-color: transparent;
  border-top-color: #484848
}

.cke_hc .cke_toolbox_collapser .cke_arrow {
  font-size: 8px;
  width: auto;
  border: 0;
  margin-top: 0
}

.cke_menuitem span {
  cursor: default
}

.cke_menubutton {
  display: block
}

.cke_hc .cke_menubutton {
  padding: 2px
}

.cke_menubutton:hover,
.cke_menubutton:focus,
.cke_menubutton:active {
  background-color: #e9e9e9;
  display: block;
  outline: 1px dotted
}

.cke_menubutton:hover {
  outline: 0
}

.cke_hc .cke_menubutton:hover,
.cke_hc .cke_menubutton:focus,
.cke_hc .cke_menubutton:active {
  border: 2px solid;
  padding: 0
}

.cke_menubutton_disabled:hover,
.cke_menubutton_disabled:focus,
.cke_menubutton_disabled:active {
  background-color: transparent;
  outline: 0
}

.cke_menubutton_inner {
  display: table-row
}

.cke_menubutton_icon,
.cke_menubutton_label,
.cke_menuarrow {
  display: table-cell
}

.cke_menubutton_icon {
  background-color: #f8f8f8;
  padding: 6px 4px
}

.cke_hc .cke_menubutton_icon {
  height: 16px;
  width: 0;
  padding: 4px 0
}

.cke_menubutton:hover .cke_menubutton_icon,
.cke_menubutton:focus .cke_menubutton_icon,
.cke_menubutton:active .cke_menubutton_icon {
  background-color: #e9e9e9
}

.cke_menubutton_disabled:hover .cke_menubutton_icon,
.cke_menubutton_disabled:focus .cke_menubutton_icon,
.cke_menubutton_disabled:active .cke_menubutton_icon {
  background-color: #f8f8f8;
  outline: 0
}

.cke_menuitem .cke_menubutton_on {
  background-color: #e9e9e9;
  border: 1px solid #dedede;
  outline: 0
}

.cke_menubutton_on .cke_menubutton_icon {
  padding-right: 3px;
  background-color: #e9e9e9
}

.cke_menubutton_label {
  padding: 0 5px;
  background-color: transparent;
  width: 100%;
  vertical-align: middle
}

.cke_menubutton_shortcut {
  color: #979797
}

.cke_menubutton_disabled .cke_menubutton_label {
  opacity: .3;
  filter: "alpha(opacity 30)";
}

.cke_panel_frame .cke_menubutton_label {
  display: none
}

.cke_menuseparator {
  background-color: #d1d1d1;
  height: 1px
}

.cke_menuarrow {
  padding: 0 5px
}

.cke_rtl .cke_menuarrow {
  background-position: 5px -13px;
  background-repeat: no-repeat
}

.cke_hc .cke_menuarrow {
  background-image: none
}

.cke_menuarrow span {
  display: none
}

.cke_hc .cke_menuarrow span {
  vertical-align: middle;
  display: inline
}

.cke_combo {
  display: inline-block;
  float: left;
  position: relative;
  margin-bottom: 5px
}

.cke_rtl .cke_combo {
  float: right
}

.cke_hc .cke_combo {
  margin-top: 1px;
  margin-bottom: 10px
}

.cke_combo:after {
  content: "";
  position: absolute;
  height: 18px;
