// Cloud FormBuilder - autocomplete

.cloud-autocomplete {
  position: relative;

  &.search-tag {
    span.input-autocomplete {
      display: none;
    }
  }

  ul {
    background-color: lighten($gray-lighter-plus, 5%);
    border: 1px solid $border-clr;
    border-top: 0;
    position: absolute !important;
    left: 0 !important;
    right: 0 !important;
    width: auto !important;
    list-style: none;
    z-index: $modal-index;
    box-shadow: 0px 5px 10px -5px rgba(0, 0, 0, 0.2);
    padding-left: 0;
    height: 200px;
    overflow-y: auto;

    li {
      cursor: pointer;

      a {
        display: block;
        text-transform: none;
        text-decoration: none;
        white-space: normal;
        padding: 5px 10px;

        &:hover {
          background-color: $control-bg;
        }
      }
    }
  }

  .input-group {
    .input-group-text {
      padding-left: .95em;
      padding-right: .95em;
    }

    .ui-autocomplete-input {
      border-right: none;
    }
  }

  button .icon-search {
    cursor: text;
    margin: 0;
  }
}

.cloud-panel .cloud-autocomplete {
  ul {
    background-color: $control-bg;

    li a:hover {
      background-color: lighten($gray-lighter-plus, 5%);
    }
  }
}
