.cloud-tree {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 200px;
    background-color: #fff;
    border: 1px solid #c5c6c5;
    z-index: 1;

    .root {
        height: 100%;
        overflow: auto;

        .items {
            .item {
                &:hover {
                    cursor: pointer;
                }

                .items {
                    background-color: #f2f2f2;

                    .item {
                        border-bottom: 1px solide #fff;
                        padding-left: 10px;
                    }
                }
            }

            .show-children {
                width: 18px;
                height: 18px;

                &:before {
                    content: '';
                    height: 8px;
                    width: 8px;
                    display: block;
                    border-right: 1px solid #333;
                    border-bottom: 1px solid #333;
                    transform: rotate(-45deg);
                    margin-top: 6px;
                }

                &.shown {
                    &:before {
                        transform: rotate(45deg);
                    }
                }
            }
        }
    }
}