// Cloud - Notifier

.notification-alert {
    // &:extend(#cloud-ui .bb5-ui-reset);
    top: 0;
    left: 0;
    position: fixed;
    width: 100%;
    text-align: center;
    padding: 20px 30px;
    background-color: $cloud-brand;
    color: #fff;
    text-transform: uppercase;
    .icon {
        float: right;
    }
    z-index: $ui-main-nav-index + 10
}

#cloud-notifier-wrapper {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: $ui-main-nav-index - 1;

    @media(max-width: 1024px) {
        top: 0;
        left: 0;
        right: 0;
        bottom: auto;
    }

    > div {
        @extend .clearfix;
    };

    .cloud-notifier {
        display:block;
        font-family: $font-family-default;
        text-transform: uppercase;
        position: relative;
        margin-bottom:20px;
        padding: 14px 25px;
        text-align: center;

        opacity:0;
        @include transition(200ms linear all);

        @media(max-width: 1024px) {
            margin-bottom:0px;
            display: block;
            float: none;
        }

        &, p, .icon {
            color:#fff;
            font-size: 15px;
        }

        .icon {
            float:left;
            margin-right: 1em;
            @include scale(1.2);
        }

        &.success {
            background-color: $success-clr;
        }

        &.info {
            background-color: $info-clr;
        }

        &.warning {
            background-color: $warning-clr;
        }

        &.error {
            background-color: $error-clr;
        }

        .cloud-progress {
            height: 2px;
            position:absolute;
            left:0;
            bottom:0;
            right:0;
            background-color: $progress-bg-clr;
            &:before {
                content: "";
                display: block;
                height: 2px;
                background-color: $progress-cursor-clr;
                @include transition(1s linear all);
                width: 0;
            }
        }
    }
}

.animating {

    @include transition(200ms linear all);
    overflow: hidden;

    .cloud-notifier {
        @include scale(1);
        opacity:1!important;

        .cloud-progress {
            &:before {
                width: 100%!important;
            }
        }
    }
    &.animating-out {
        .cloud-notifier {
            @include translate3d(200px, 0, 0);
            opacity: 0!important;
        }
    }
}
