// Cloud FormBuilder - MailChimp

.mailchimp-connector {
    .connect-area {
        button {
            width: 100%;
        }
    }

    .connected-area {
        p {
            margin: 5px 0;

            &.user-area {
                font-weight: bold;
            }

            &.warning {
                color: $warning-clr;
            }
        }
    }
}